import { Fragment, useEffect, useState, useMemo } from "react";
import { Col, Form, Row, Spin, Dropdown, Menu } from "antd";
import { Link } from "react-router-dom";
import { ReactComponent as ExportIcon } from "../../../../assets/icons/export-icon-big.svg";
import { ReactComponent as PrintIcon } from "../../../../assets/icons/print-icon.svg";
import { ReactComponent as MailIcon } from "../../../../assets/icons/email-icon.svg";
import { ReactComponent as DownloadPDFIcon } from "../../../../assets/icons/download-pdf-icon.svg";
import VisibilityListFilter from "./VisibilityListFilter";
import { CaretDownOutlined } from "@ant-design/icons";

import CommonModal from "../../../components/Modals/CommonModal";
import ShipmentPrintTabs from "../../Print/Shipment/ShipmentPrintTabs";
import PrintZoomButton from "../../../components/Inputs/PrintZoomButton";
import VisibilityExportForm from "./VisibilityExportForm";
import VisibilityExportPDF from "./VisibilityExportPDF";
import SvgConverter from "../../../components/Common/SvgConverter";
import { pdf } from "@react-pdf/renderer";
import moment from "moment";
import {
  convertBlobToBase64,
  dataURLtoFile,
  triggerNotification,
} from "../../../../utilz/utilz";
import { saveAs } from "file-saver";
import { useDispatch, useSelector } from "react-redux";
import FormInput from "../../../components/Inputs/FormInput";
import { sendMailShipmentDocumentInProgress } from "../../../../redux/shipment/ShipmentActions";
import { NOTIFICATION_TYPES } from "../../../../contants/common";
import {
  SHIPMENT_PRINTS_ENABLED_STATUS,
  SHIPMENT_PRINTS_TAB_ID,
  SHIPMENT_STATUS,
} from "../../../../contants/ShipmentEnums";
import {
  getVisibilityExportDetails,
  sendEmailVisibility,
} from "../../../../redux/visibility/VisibilityActions";
import { useLocation } from "react-router-dom";
import { routeFileSelector } from "../../../../utilz/i18n";
import { useTranslation } from "react-i18next";

const VisibilityHeader = () => {
  const [exportModal, setExportModal] = useState(false);
  const [exportForm] = Form.useForm();
  const { selectedCompany } = useSelector((state) => state.auth);
  const [printActiveTab, setPrintActiveTab] = useState("1");
  const [zoomValue, setZoomValue] = useState(1);
  const [printModalOpen, setPrintModalOpen] = useState(false);
  const { selectedVisibility, exportDetails, visibilitySearchList } =
    useSelector((state) => state.visibility);
  const [form] = Form.useForm();
  const [isSentEmail, setIsSentEmail] = useState(false);
  const { shipmentDetails } = useSelector((state) => state.shipment);
  const {
    user: { data: userDetails },
  } = useSelector((state) => state?.auth);
  const { notificationAPI } = useSelector((state) => state.common);
  const dispatch = useDispatch();
  const [isClickedDownloadPDF, setIsClickedDownloadPDF] = useState(false);
  const [isClickedPrintPDF, setIsClickedPrintPDF] = useState(false);
  const [isClickedEmail, setIsClickedEmail] = useState(false);
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);
  const handlePrintClick = (tabId) => {
    setPrintActiveTab(tabId);
    setPrintModalOpen(true);
  };

  const getPrintMenus = (status) => {
    return [
      {
        key: "1",
        label: (
          <Link
            to={false}
            onClick={() =>
              handlePrintClick(SHIPMENT_PRINTS_TAB_ID.COMMERCIAL_INVOICE)
            }
          >
            {t('translation:default.fields.viewCI')}
          </Link>
        ),
      },
      {
        key: "2",
        label: (
          <Link
            to={false}
            onClick={() =>
              handlePrintClick(SHIPMENT_PRINTS_TAB_ID.PACKING_LIST)
            }
          >
            {t('translation:default.fields.viewPL')}
          </Link>
        ),
      },
      {
        key: "3",
        label: (
          <Link
            to={false}
            onClick={() => handlePrintClick(SHIPMENT_PRINTS_TAB_ID.BOL)}
          >
            {t('translation:default.fields.viewBOL')}
          </Link>
        ),
      },
    ];
  };

  const printMenuItems = useMemo(() => [...getPrintMenus()]);

  const menu = <Menu items={printMenuItems} />;

  useEffect(() => {
    if (selectedCompany.companyName !== "") {
      form.setFieldValue(
        "subject",
        `${t('translation:visibility.header.sendEmailSubtitle')} ${selectedCompany?.companyName || ""}`
      );
    }
  }, [selectedCompany]);

  const downloadPdf = async () => {
    if (Object.keys(selectedVisibility?.data).length === 0) {
      return false;
    }
    dispatch(
      getVisibilityExportDetails({
        // customerKey:
        //   userDetails?.userType === "CUST"
        //     ? `${selectedCompany.companyId}`
        //     : "",
        // carrierKey:
        //   userDetails?.userType === "CARR"
        //     ? `${selectedCompany.companyId}`
        //     : "",
        // vendorKey:
        //   userDetails?.userType === "VEND"
        //     ? `${selectedCompany.companyId}`
        //     : "",
        // shipmentType: visibilitySearchList.req?.shipmentType,
        shipmentNo: selectedVisibility.data.shipmentNo,
      })
    );
    setIsClickedDownloadPDF(true);
    // const fileName = `visibility_export_${
    //   selectedCompany?.companyId
    // }_${moment().format("MMDDYYYY")}_${moment().format("HHmm")}.pdf`;
    // const blob = await pdf(
    //   <VisibilityExportPDF data={selectedVisibility?.data || {}} />
    // ).toBlob();
    // const fileBase64 = await convertBlobToBase64(blob);
    // saveAs(blob, fileName);
  };

  useEffect(() => {
    const downloadPdfAction = async () => {
      const fileName = `visibility_export_${
        selectedCompany?.companyId
      }_${moment().format("MMDDYYYY")}_${moment().format("HHmm")}.pdf`;
      const blob = await pdf(
        <VisibilityExportPDF
          shipmentdata={selectedVisibility?.data || {}}
          data={exportDetails?.data || {}}
        />
      ).toBlob();
      const fileBase64 = await convertBlobToBase64(blob);
      saveAs(blob, fileName);
    };

    const printPDFAction = async () => {
      const blob = await pdf(
        <VisibilityExportPDF
          shipmentdata={selectedVisibility?.data || {}}
          data={exportDetails?.data || {}}
        />
      ).toBlob();
      var file = new Blob([blob], { type: "application/pdf" });
      var fileURL = URL.createObjectURL(file);
      window.open(fileURL, "_blank");
    };

    if (
      Object.keys(exportDetails.data).length > 0 &&
      exportDetails.progressing === false
    ) {
      if (isClickedDownloadPDF) {
        downloadPdfAction();
        setIsClickedDownloadPDF(false);
      }
      if (isClickedPrintPDF) {
        printPDFAction();
        setIsClickedPrintPDF(false);
      }
      if (isClickedEmail) {
        setIsClickedEmail(false);
        form.resetFields();
        form.setFieldValue(
          "subject",
          `${t('translation:visibility.header.sendEmailSubtitle')} ${selectedCompany?.companyName || ""}`
        );
        setIsSentEmail(true);
        handleEMailAction();
      }
    }
  }, [exportDetails]);

  useEffect(() => {
    if (isClickedEmail === true) {
      dispatch(
        getVisibilityExportDetails({
          // customerKey:
          //   userDetails?.userType === "CUST"
          //     ? `${selectedCompany.companyId}`
          //     : "",
          // carrierKey:
          //   userDetails?.userType === "CARR"
          //     ? `${selectedCompany.companyId}`
          //     : "",
          // vendorKey:
          //     userDetails?.userType === "VEND"
          //       ? `${selectedCompany.companyId}`
          //       : "",
          // shipmentType: visibilitySearchList.req?.shipmentType,
          shipmentNo: selectedVisibility.data.shipmentNo,
        })
      );
    }
  }, [isClickedEmail]);

  const printPDF = async () => {
    if (Object.keys(selectedVisibility?.data).length === 0) {
      return false;
    }
    dispatch(
      getVisibilityExportDetails({
        // customerKey:
        //   userDetails?.userType === "CUST"
        //     ? `${selectedCompany.companyId}`
        //     : "",
        // carrierKey:
        //   userDetails?.userType === "CARR"
        //     ? `${selectedCompany.companyId}`
        //     : "",
        // vendorKey:
        //     userDetails?.userType === "VEND"
        //       ? `${selectedCompany.companyId}`
        //       : "",
        // shipmentType: visibilitySearchList.req?.shipmentType,
        shipmentNo: selectedVisibility.data.shipmentNo,
      })
    );
    setIsClickedPrintPDF(true);
    // const blob = await pdf(
    //   <VisibilityExportPDF data={selectedVisibility?.data || []} />
    // ).toBlob();
    // var file = new Blob([blob], { type: "application/pdf" });
    // var fileURL = URL.createObjectURL(file);
    // window.open(fileURL, "_blank");
  };

  const handleEmailSubmit = async (data) => {
    try {
      const formData = new FormData();
      const fileName = `VISIBILITY_${
        shipmentDetails?.data?.shipmentNo
          ? shipmentDetails?.data?.shipmentNo
          : ""
      }_${moment().format("MMDDYYYY")}_${moment().format("HHmm")}.pdf`;
      const blob = await pdf(
        <VisibilityExportPDF data={exportDetails?.data || {}} />
      ).toBlob();
      const fileBase64 = await convertBlobToBase64(blob);
      var file = dataURLtoFile(fileBase64, fileName);
      formData.append("attachment", file);
      formData.append("toEmail", data?.email);
      formData.append("subject", data.subject);
      formData.append("Body", data.comments);
      dispatch(sendEmailVisibility(formData));
      setIsSentEmail(false);
    } catch (error) {
      triggerNotification(notificationAPI, {
        type: NOTIFICATION_TYPES.ERROR,
        title: t('translation:visibility.header.notificationSendEmailError'),
      });
    }
  };

  const handleEMailAction = async () => {};

  return (
    <Fragment>
      <Row
        justify="space-between"
        className={`py-15 px-14 ${
          Object.keys(selectedVisibility?.data).length === 0 ? "opacity-2" : ""
        }`}
        align="middle"
      >
        <Col sm={10} className="flex-1">
          <Row align={"middle"} gutter={20}>
            <Col className="flex-1">
              <VisibilityListFilter />
            </Col>
          </Row>
        </Col>
        <Col sm={14}>
          {exportDetails.progressing == true && (
            <div className="d-flex justify-content-center">
              <Spin spinning={true} tip={t('translation:default.loading')}></Spin>
            </div>
          )}
          {exportDetails.progressing == false && (
            <Row gutter={6} className="d-flex justify-content-end">
              <Col>
                <div
                  className={`d-flex align-items-center pr-15 ${
                    Object.keys(selectedVisibility?.data).length === 0
                      ? ""
                      : "cursor-pointer"
                  }`}
                  onClick={() => {
                    if (Object.keys(selectedVisibility?.data).length > 0) {
                      setExportModal(true);
                    }
                  }}
                >
                  <ExportIcon />
                  <label
                    className={`ml-5 cursor-pointer ${
                      Object.keys(selectedVisibility?.data).length === 0
                        ? ""
                        : "cursor-pointer"
                    }`}
                  >
                    {t('translation:default.buttons.export')}
                  </label>
                </div>
              </Col>
              <Col>
                <div
                  className={`d-flex align-items-center pr-15 ${
                    Object.keys(selectedVisibility?.data).length === 0
                      ? ""
                      : "cursor-pointer"
                  }`}
                  onClick={() => downloadPdf()}
                >
                  <DownloadPDFIcon />
                  <label
                    className={`ml-5 ${
                      Object.keys(selectedVisibility?.data).length === 0
                        ? ""
                        : "cursor-pointer"
                    }`}
                  >
                    {t('translation:visibility.header.downloadLabel')}
                  </label>
                </div>
              </Col>
              <Col>
                <div
                  className={`d-flex align-items-center pr-15 ${
                    Object.keys(selectedVisibility?.data).length === 0
                      ? ""
                      : "cursor-pointer"
                  }`}
                  onClick={() => printPDF()}
                >
                  <PrintIcon />
                  <label
                    className={`ml-5 ${
                      Object.keys(selectedVisibility?.data).length === 0
                        ? ""
                        : "cursor-pointer"
                    }`}
                  >
                     {t('translation:visibility.header.printLabel')}
                  </label>
                </div>
              </Col>
              <Col>
                <div
                  className={`d-flex align-items-center pr-15 ${
                    Object.keys(selectedVisibility?.data).length === 0
                      ? ""
                      : "cursor-pointer"
                  }`}
                  onClick={() => {
                    if (Object.keys(selectedVisibility?.data).length === 0) {
                      return false;
                    }
                    setIsClickedEmail(true);
                  }}
                >
                  <MailIcon />
                  <label
                    className={`ml-5 ${
                      Object.keys(selectedVisibility?.data).length === 0
                        ? ""
                        : "cursor-pointer"
                    }`}
                  >
                    {t('translation:visibility.header.emailLabel')}
                  </label>
                </div>
              </Col>
              {/* <Col>
                <div className={`d-flex align-items-center`}>
                  <Dropdown
                    overlay={<></>}
                    {...(printMenuItems.length > 0 ? { overlay: menu } : {})}
                    placement="bottomRight"
                  >
                    <span className="border-none p-0 m-0 d-flex align-items-center">
                      <PrintIcon />
                      <label className={`ml-5`}>Print/View</label>
                      {printMenuItems.length > 0 && (
                        <span>
                          <CaretDownOutlined />
                        </span>
                      )}
                    </span>
                  </Dropdown>
                </div>
              </Col> */}
              {/* <Col>
                <div
                  className={`d-flex align-items-center pr-15 ${
                    Object.keys(selectedVisibility?.data).length === 0
                      ? ""
                      : "cursor-pointer"
                  }`}
                >
                  <Dropdown
                    overlay={<></>}
                    {...(printMenuItems.length > 0 ? { overlay: menu } : {})}
                    placement="bottomRight"
                  >
                    <a className="border-none p-0 m-0 text-white d-flex align-items-center">
                      <span className="mr-10">
                        <SvgConverter
                          Icon={PrintIcon}
                          color={"white"}
                          width={24}
                        />{" "}
                      </span>
                      <span className="mr-3 f-18 mt-n2">Print/View</span>
                      {printMenuItems.length > 0 && (
                        <span>
                          <CaretDownOutlined />
                        </span>
                      )}
                    </a>
                  </Dropdown>
                </div>
              </Col> */}
            </Row>
          )}
        </Col>
        <CommonModal
          className="vw-85 h-84"
          open={exportModal}
          title={t('translation:visibility.header.visibilityExport')}
          okText={t('translation:default.buttons.export')}
          cancelText={t('translation:default.buttons.close')}
          onCancel={() => setExportModal(false)}
          onClose={() => setExportModal(false)}
          onOk={() => exportForm.submit()}
        >
          <VisibilityExportForm form={exportForm} />
        </CommonModal>
        <CommonModal
          className="vw-40 h-45"
          open={isSentEmail}
          title={t('translation:visibility.header.sendEmailTitle')}
          okText={t('translation:default.buttons.sendEmail')}
          cancelText={t('translation:default.buttons.cancel')}
          onCancel={() => {
            setIsSentEmail(false);
          }}
          onClose={() => {
            setIsSentEmail(false);
          }}
          onOk={() => form.submit()}
        >
          <div className="h-100 d-flex flex-direction-column">
            <div className="custom-scroll-bar">
              <Form
                onFinish={(data) => handleEmailSubmit(data)}
                form={form}
                initialValues={{ email: "", subject: "", comments: "" }}
              >
                <Row>
                  <Col sm={24}>
                    <FormInput
                      editMode={true}
                      label={t('translation:default.fields.emailAddress')}
                      placeholder={t('translation:visibility.header.sendEmailAddress.placeholder')}
                      name="email"
                      rules={[
                        { required: true, message:  t('translation:visibility.header.sendEmailAddress.errorMessage') },
                      ]}
                      maxLength={50}
                    />
                  </Col>
                </Row>
                <Row>
                  <Col sm={24}>
                    <FormInput
                      editMode={false}
                      label={t('translation:visibility.header.sendEmailSubject.label')}
                      placeholder={t('translation:visibility.header.sendEmailSubject.label')}
                      name="subject"
                    />
                  </Col>
                </Row>
                <Row>
                  <Col sm={24}>
                    <FormInput
                      editMode={true}
                      label={t('translation:visibility.header.sendEmailComments.label')}
                      placeholder={t('translation:visibility.header.sendEmailComments.placeholder')}
                      name="comments"
                    />
                  </Col>
                </Row>
              </Form>
            </div>
          </div>
        </CommonModal>
        <CommonModal
          onClose={() => setPrintModalOpen(false)}
          open={printModalOpen}
          className={"w-65"}
          customHeader={
            <Row
              className="flex-1"
              gutter={15}
              justify={"space-between"}
              align={"middle"}
            >
              <Col className="flex-1">
                <label className="title">{t('translation:visibility.header.documents')}</label>
              </Col>
              <Col className="flex-1">
                <PrintZoomButton onChange={(val) => setZoomValue(val)} />
              </Col>
              <Col className="flex-1"></Col>
            </Row>
          }
          contentClassName={"p-0"}
          customFooter={<></>}
          descriptionClassName={"m-0"}
        >
          <ShipmentPrintTabs
            printActiveTab={printActiveTab}
            setPrintActiveTab={setPrintActiveTab}
            zoomValue={zoomValue}
          />
        </CommonModal>
      </Row>
    </Fragment>
  );
};

export default VisibilityHeader;
