import { Collapse } from "antd";
import { useState, useMemo } from "react";
import VisibilityShipmentSearchByOrigin from "./VisibilityShipmentSearchByOrigin";
import VisibilityShipmentSearch from "./VisibilityShipmentSearch";
import VisibilityShipmentSearchByOrder from "./VisibilityShipmentSearchByOrder";
import { useLocation } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { routeFileSelector } from '../../../../../utilz/i18n';

const { Panel } = Collapse;

const VisibilityFilterContent = ({ form }) => {
  const [activeKey, setActiveKey] = useState("1");

 const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);

  const handleTabClick = (keys) => {
    if (keys && keys[1]) {
      setActiveKey(keys[1]);
    } else {
      setActiveKey("");
    }
  };

  return (
    <Collapse
      ghost
      className="filter-collapse-container px-16"
      activeKey={activeKey}
      onChange={handleTabClick}
    >
      <Panel header={t('translation:visibility.filters.titles.shipmentSearch')} key="1">
        <VisibilityShipmentSearch form={form} />
      </Panel>
      <Panel header={t('translation:visibility.filters.titles.byOrigin')} key="2">
        <VisibilityShipmentSearchByOrigin form={form} />
      </Panel>
      <Panel header={t('translation:visibility.filters.titles.byOrder')} key="3">
        <VisibilityShipmentSearchByOrder form={form} />
      </Panel>
    </Collapse>
  );
};

export default VisibilityFilterContent;
