import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getShipmentTypesEffect } from "../../../redux/shipment/ShipmentEffects";
import { getSupplierType } from "../../../redux/visibility/VisibilityActions";
import FormSelectBox from "../Inputs/FormSelectBox";import i18n from 'i18next';


const SupplierTypeSelector = ({
  placeholder = i18n.t('translation:default.fields.supplier.placeholder'),
  label = i18n.t('translation:default.fields.supplier.label'),
  rules = [],
  name,
  editMode,
  margin,
  tooltip = false,
  customerKey = null
}) => {
  const [options, setOptions] = useState([]);
  const { selectedCompany } = useSelector((state) => state.auth);
  const { supplierList } = useSelector((state) => state.visibility);
  const dispatch = useDispatch();

  /*useEffect(() => {
    if (selectedCompany && selectedCompany.companyId) {
      dispatch(getSupplierType({ CustomerKey: selectedCompany.companyId }));
    }
  }, [selectedCompany]); */

  useEffect(() => {
    if(customerKey != '' && customerKey != null){
      dispatch(getSupplierType({ CustomerKey: customerKey }));
    }
  }, [customerKey])
  

  useEffect(() => {
    if (supplierList.data && supplierList.success === true) {
      try {
        let options = supplierList.data.map((row) => ({
          label: row.vendorName,
          value: row.vendorKey,
        }));
        setOptions(options);
      } catch (error) {
        setOptions([]);
      }
    }
  }, [supplierList]);

  return (
    <FormSelectBox
      loading={false}
      options={options}
      placeholder={placeholder}
      label={label}
      rules={rules}
      name={name}
      editMode={editMode}
      margin={margin}
      tooltip={tooltip}
    />
  );
};

export default SupplierTypeSelector;
