import { Fragment, useEffect, useMemo, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Row, Col, Form } from "antd";
import {
  getVendorShipperListInProgress,
  setSelectedShipper,
} from "../../../../../redux/POV/POVActions";
import ShipmentEntrySelector from "../../../../components/Selectors/ShipmentEntrySelector";
import CountrySelector from "../../../../components/Selectors/CountrySelector";
import FormInput from "../../../../components/Inputs/FormInput";
import StateSelector from "../../../../components/Selectors/StateSelector";
import CitySelector from "../../../../components/Selectors/CitySelector";
import ShipmentConsigneeSearchSelector from "../../../../components/Selectors/AsynchSearch/ShipmentConsigneeSearchSelector";
import PostalCodeInput from "../../../../components/Selectors/PostalCodeInput";
import HiddenFormInput from "../../../../components/Inputs/HiddenFormInput";
import ShipperConsigneeSearchSelector from "./ShipperConsigneeSearchSelector";
import { useLocation } from "react-router-dom";
import { routeFileSelector } from "../../../../../utilz/i18n";
import { useTranslation } from "react-i18next";

const VisibilityShipmentSearchByOrigin = ({ form, type = 2 }) => {
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);
  
  const roleType =
    type === 1
      ? { short: "O", full: "shipper", key: "origin", label: t('translation:default.fields.shipper')}
      : { short: "C", full: "consignee", key: "dest", label: t('translation:default.fields.consignee')};

  const [selectedShipperData, setSelectedShipperData] = useState([]);
  const [loading, setLoading] = useState(false);
  const {
    selectedCompany,
    user: { data: userDetails },
  } = useSelector((state) => state.auth);
  const customerKey = form.getFieldValue("customerKey");

  const { vendorShipperList } = useSelector((state) => state.poVendor);
  const dispatch = useDispatch();

  const onchangeShipperData = (shipData) => {
    setSelectedShipperData(shipData?.data);
    dispatch(setSelectedShipper(shipData?.data));
  };

  const handleCompanyChange = ({
    countryCode,
    zip,
    state,
    city,
    name,
    shipRecKey,
  }) => {
    form.setFieldsValue({
      [`${roleType.key}Country`]: countryCode,
      [`${roleType.key}Zip`]: zip,
      [`${roleType.key}State`]: state,
      [`${roleType.key}City`]: city,
      [`${roleType.full}Name`]: name,
      [`${roleType.full}Key`]: shipRecKey,
    });
  };

  const [isValidPostal, setIsValidPostal] = useState(true);

  const shipperCountryData = Form.useWatch("shipperCountry", form);
  const shipperStateData = Form.useWatch("shipperState", form);

  const consigneeCountryData = Form.useWatch("consigneeCountry", form);
  const consigneeStateData = Form.useWatch("consigneeState", form);

  const handleCountryChange = () => {
    form.setFieldsValue({
      shipperZip: undefined,
      shipperState: undefined,
      shipperCity: undefined,
    });
  };

  const handleConsigneeCountryChange = () => {
    form.setFieldsValue({
      consigneeState: "",
      consigneeCity: "",
      consigneeZip: "",
    });
  };

  const handleZipDetails = (data) => {
    if (data) {
      setIsValidPostal(true);
      form.setFieldsValue({
        shipperState: data.state.trim(),
        shipperCity: data.city.trim(),
      });
      form.validateFields();
    } else {
      setIsValidPostal(false);
      form.validateFields();
    }
  };

  useEffect(() => {
    if (selectedCompany?.companyId) {
      const payloadDat = {
        vendorKey: selectedCompany ? selectedCompany.companyId : "",
      };
      dispatch(getVendorShipperListInProgress(payloadDat));
    }
  }, [selectedCompany]);

  return (
    <Fragment>
      <Row className="pt-10">
        <Col sm={24}>
          <ShipperConsigneeSearchSelector
            editMode={true}
            name={"shipperName"}
            placeholder={t('translation:default.fields.shipperName.label')}
            form={form}
            onChange={onchangeShipperData}
            searchType="shipper"
            userType={userDetails.userType}
            margin={"mb-15"}
          />
        </Col>
        <Col sm={24}>
          <CountrySelector
            label={t('translation:default.fields.shipperCountry.label')}
            editMode={true}
            margin={"mb-15"}
            name={"shipperCountry"}
            placeholder={t('translation:default.fields.shipperCountry.placeholder')}
            onChange={handleCountryChange}
          />
        </Col>
        <Col sm={24}>
          <PostalCodeInput
            label={t('translation:default.fields.shipperPostalCode')}
            editMode={true}
            margin={"mb-15"}
            name={"shipperZip"}
            placeholder={t('translation:default.fields.shipperPostalCode')}
            handleZipDetails={handleZipDetails}
            countryCode={shipperCountryData}
          />
        </Col>
        <Col sm={24}>
          <StateSelector
            label={t('translation:default.fields.shipperStateProvince.label')}
            margin={"mb-15"}
            name={"shipperState"}
            placeholder={t('translation:default.fields.shipperStateProvince.placeholder')}
            screen={"CreateShipment"}
            countryCode={shipperCountryData}
          />
        </Col>
        <Col sm={24}>
          <CitySelector
            label={t('translation:default.fields.shipperCity.label')}
            editMode={true}
            margin={"mb-15"}
            name={"shipperCity"}
            placeholder={t('translation:default.fields.shipperCity.placeholder')}
            countryCode={shipperCountryData}
            stateCode={shipperStateData}
          />
        </Col>
        <Col sm={24} className="mb-15">
          {/* <ShipmentConsigneeSearchSelector
            label="consignee Name"
            placeholder="consignee Name"
            form={form}
            name={`${roleType.full}Name`}
            onChange={handleCompanyChange}
            userType={userDetails.userType}
            customerKey={customerKey || ""}
          /> */}
          <ShipperConsigneeSearchSelector
            label={t('translation:default.fields.consigneeName.label')}
            editMode={true}
            name={`${roleType.full}Name`}
            placeholder={t('translation:default.fields.consigneeName.label')}
            form={form}
            onChange={onchangeShipperData}
            searchType="consignee"
            userType={userDetails.userType}
            margin={"mb-0"}
          />
        </Col>
        <HiddenFormInput name={"consigneeKey"} />
        <Col sm={24}>
          <CountrySelector
            label={t('translation:default.fields.consigneeCountry.label')}
            margin={"mb-15"}
            editMode={true}
            name={"consigneeCountry"}
            placeholder={t('translation:default.fields.consigneeCountry.placeholder')}
            onChange={handleConsigneeCountryChange}
          />
        </Col>
        <Col sm={24}>
          <FormInput
            margin="mb-15"
            label={t('translation:default.fields.consigneePostalCode.label')}
            name={"consigneeZip"}
            placeholder={t('translation:default.fields.consigneePostalCode.label')}
            editMode={true}
          />
        </Col>
        <Col sm={24}>
          <StateSelector
            label={t('translation:default.fields.consigneeState.label')}
            margin={"mb-15"}
            name={"consigneeState"}
            placeholder={t('translation:default.fields.consigneeState.placeholder')}
            screen={"CreateShipment"}
            countryCode={consigneeCountryData}
          />
        </Col>
        <Col sm={24}>
          <CitySelector
            label={t('translation:default.fields.consigneeCity.label')}
            editMode={true}
            margin={"mb-15"}
            name={"consigneeCity"}
            placeholder={t('translation:default.fields.consigneeCity.placeholder')}
            countryCode={consigneeCountryData}
            stateCode={consigneeStateData}
          />
        </Col>
      </Row>
    </Fragment>
  );
};

export default VisibilityShipmentSearchByOrigin;
