const breakLongWord = (word, maxLength) => {
  word = word + "";
  const words = [];
  let currentWord = "";

  if (word.length == 0) {
    return [];
  }

  for (let i = 0; i < word.length; i++) {
    currentWord += word[i];

    if (currentWord.length === maxLength) {
      words.push(currentWord);
      currentWord = "";
    } else if (currentWord.length > maxLength) {
      words.push(currentWord.slice(0, -1));
      currentWord = word[i];
    }
  }

  if (currentWord.length > 0) {
    words.push(currentWord);
  }

  return words;
};

