import { Col, Form, Row } from "antd";
import { useEffect, useMemo } from "react";
import { useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { SORTING_ORDERS } from "../../../../contants/common";
import IconButton from "../../../components/Buttons/IconButton";
import SvgConverter from "../../../components/Common/SvgConverter";
import FormSelectBox from "../../../components/Inputs/FormSelectBox";
import { ReactComponent as FilterAscIcon } from "../../../../assets/icons/filter-asc.svg";
import { ReactComponent as FilterDescIcon } from "../../../../assets/icons/filter-desc.svg";
import {
  getSearchVisibilityList,
  getSearchVisibilityListClear,
  getVisibilityExceptionClear,
  getSearchVisibilitySetDataList,
} from "../../../../redux/visibility/VisibilityActions";
import { setShipmentData } from "../../../../redux/shipment/ShipmentActions";
import { SORTING_FILEDS_VISIBILITY } from "../../../../contants/VisibilityEnums";
import { useLocation } from "react-router-dom";
import { routeFileSelector } from "../../../../utilz/i18n";
import { useTranslation } from "react-i18next";

const VisibilityListFilter = () => {
  const [selectedOrderType, setSelectedOrderType] = useState(
    SORTING_ORDERS.ASC
  );
  const [form] = Form.useForm();
  const orderByColumn = Form.useWatch("orderByColumn", form);
  const dispatch = useDispatch();
  const { selectedCompany } = useSelector((state) => state.auth);
  const { visibilitySearchList } = useSelector((state) => state.visibility);
  const {
    user: { data: userDetails },
  } = useSelector((state) => state?.auth);
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);

  const handleSortingToggle = (oldType) => {
    let sortingType;
    if (oldType === SORTING_ORDERS.ASC) {
      sortingType = SORTING_ORDERS.DESC;
    } else if (oldType === SORTING_ORDERS.DESC) {
      sortingType = SORTING_ORDERS.ASC;
    }
    return sortingType;
  };

  useEffect(() => {
    form.setFieldValue("orderByColumn", null);
  }, []);

  useEffect(() => {
    if (selectedCompany) {
      dispatch(getSearchVisibilityListClear());
      dispatch(getVisibilityExceptionClear());
      dispatch(setShipmentData([]));
      //getSearchVisibilityAPI();
      // dispatch(
      //   getFooterContentReq({
      //     shipmentType: "A",
      //     customerKey:
      //       userDetails?.userType === "CUST"
      //         ? `${selectedCompany?.companyId}`
      //         : "",
      //     carrierKey:
      //       userDetails?.userType === "CARR"
      //         ? `${selectedCompany?.companyId}`
      //         : "",
      //     vendorKey: "",
      //     userType: userDetails?.userType,
      //   })
      // );
    }
  }, [selectedCompany]);

  useEffect(() => {
    if (selectedCompany?.companyId && orderByColumn) {
      getSearchVisibilityAPI();
    }
  }, [orderByColumn, selectedOrderType]);

  const getSearchVisibilityAPI = () => {
    dispatch(getSearchVisibilitySetDataList([]));
    let req = {
      orderByColumn: orderByColumn ? orderByColumn : "",
      orderBySort: orderByColumn ? selectedOrderType : "",
      statusCode: "",
    };
    if (
      userDetails?.userType === "VEND" &&
      visibilitySearchList?.req?.vendorKey == ""
    ) {
      return false;
    }
    if (
      userDetails?.userType === "CARR" &&
      visibilitySearchList?.req?.carrierKey == ""
    ) {
      return false;
    }
    if (
      userDetails?.userType === "CUST" &&
      visibilitySearchList?.req?.customerKey == ""
    ) {
      return false;
    }

    dispatch(getSearchVisibilityList(req));
  };

  return (
    <Form className="w-100" form={form}>
      <Row align="middle" gutter={15}>
        <Col sm={16} lg={16}>
          <FormSelectBox
            margin="m-0"
            className="w-100"
            placeholder={t("translation:default.fields.sortBy.label")}
            name={"orderByColumn"}
            options={SORTING_FILEDS_VISIBILITY}
            onChange={(val) => {
              if (typeof val == "undefined") {
                dispatch(
                  getSearchVisibilityList({
                    orderByColumn: "",
                    orderBySort: "",
                  })
                );
              }
            }}
          />
        </Col>
        <Col>
          <IconButton onClick={() => setSelectedOrderType(handleSortingToggle)}>
            <SvgConverter
              className="mr-5"
              Icon={
                selectedOrderType === SORTING_ORDERS.ASC
                  ? FilterAscIcon
                  : FilterDescIcon
              }
              height={18}
              width={18}
            />
          </IconButton>
        </Col>
      </Row>
    </Form>
  );
};

export default VisibilityListFilter;
