import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getVendorShipperListInProgress } from "../../../redux/POV/POVActions";
import { getShippers } from "../../../redux/POV/POVEffects";
import FormSelectBox from "../Inputs/FormSelectBox";
import i18n from 'i18next';

const ShipmentEntrySelector = ({
  placeholder = i18n.t('translation:default.fields.shipperName.placeholder'),
  label = i18n.t('translation:default.fields.shipperName.label'),
  rules = [],
  name,
  editMode,
  margin,
  singleLineMode,
  loading,
  options,
  onChange,
}) => {
  let optionData = [];
  const opData = options ? options : [];
  if (name === "consigneeName") {
    optionData = opData.map((row) => ({
      label: row.consigneeName,
      value: row.consigneeZip,
      data: row,
      address: row.consigneeAddr1,
    }));
  } else {
    optionData = opData.map((row) => ({
      label: row.name,
      value: row.vendorAddressKey,
      data: row,
      address: row.address,
    }));
  }

  return (
    <FormSelectBox
      placeholder={placeholder}
      loading={loading}
      options={optionData}
      label={label}
      rules={rules}
      name={name}
      editMode={editMode}
      margin={margin}
      singleLineMode={singleLineMode}
      onChange={onChange}
    />
  );
};

export default ShipmentEntrySelector;
