import { Spin } from "antd";
import React, { useEffect, useMemo, useState } from "react";
import { getShipmentDocumentsListInProgress } from "../../../../../redux/shipment/ShipmentActions";
import { useDispatch, useSelector } from "react-redux";
import { MODULES_ID, NOTIFICATION_TYPES } from "../../../../../contants/common";
import useDocsExtentions from "../../../../../redux/hooks/useDocsExtentions";
import _, { remove } from "lodash";
import FilesDetailsTabCard from "./FilesDetailsTabCard";
import {
  getShipmentDocumentListEffect,
  shipmentFileDeleteEffect,
  shipmentFileUpdateEffect,
  shipmentFileUploadEffect,
} from "../../../../../redux/shipment/ShipmentEffects";
import {
  getErrorMessageFromAPI,
  triggerNotification,
} from "../../../../../utilz/utilz";
import { setAppLoader } from "../../../../../redux/common/CommonActions";
import { addNewFileBtn } from "../../../../../redux/visibility/VisibilityActions";
import { useLocation } from "react-router-dom";
import { routeFileSelector } from "../../../../../utilz/i18n";
import { useTranslation } from "react-i18next";

function FileDetailsTab(props) {
  const dispatch = useDispatch();
  const { user } = useSelector((state) => state.auth);
  const notificationAPI = useSelector((state) => state.common.notificationAPI);
  const { documentDetails } = useSelector((state) => state.shipment);
  const allowedExtentions = useDocsExtentions(MODULES_ID.VISIBILITY);
  const [documentsList, setDocumentsList] = useState([]);
  const { addnewfileactions } = useSelector((state) => state.visibility);
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);

  useEffect(() => {
    if (addnewfileactions.btnCliked) {
      handleNewFile();
    }
  }, [addnewfileactions]);

  useEffect(() => {
    if (props.data?.shipmentNo) {
      let payload = {
        shipmentKey: props.data.shipmentkey,
        customerKey: props.data.customerKey,
      };
      dispatch(getShipmentDocumentsListInProgress(payload));
      dispatch(addNewFileBtn(false));
    }
  }, [props.data.shipmentNo]);

  useEffect(() => {
    if (documentDetails.success) {
      setDocumentsList(
        documentDetails.data.map((row) => ({
          ...row,
          renderKey: _.uniqueId(),
          isNew: false,
          isUpdate: false,
        }))
      );
    }
  }, [documentDetails?.success, documentDetails?.data]);

  const handleNewFile = () => {
    let data = _.cloneDeep(documentsList);
    data = [{ renderKey: _.uniqueId(), isNew: true, isUpdate: true }, ...data];
    setDocumentsList(data);
  };

  const uploadedDocumentTypes = useMemo(
    () =>
      remove(
        documentsList.map(({ fileType }) => (fileType ? fileType : false)),
        (val) => Boolean(val)
      ),
    [documentsList]
  );

  const getDynamicDocumentList = async (notificationTitle, isNew, index) => {
    try {
      const payload = {
        customerKey: props.data.customerKey,
        ShipmentKey: props.data.shipmentkey,
      };
      const { data } = await getShipmentDocumentListEffect(payload);
      if (isNew) {
        let cloneList = _.cloneDeep(documentsList);
        cloneList = cloneList.filter((item) => item.isNew == false);
        let obj = data[data.length - 1];
        let findNewRecord = documentsList.filter((item) => item.isNew == true);
        obj = {
          ...obj,
          renderKey: findNewRecord?.[0]?.renderKey || "",
          isNew: false,
          isUpdate: false,
        };
        cloneList.push(obj);
        dispatch(addNewFileBtn(false));
        setDocumentsList(cloneList);
      } else {
        let cloneList = _.cloneDeep(documentsList);
        let findItem = cloneList[index];
        let findObj = data.filter(
          (item) => item.documentSequence == findItem.documentSequence
        );
        findObj = {
          ...findObj[0],
          renderKey: findItem.renderKey,
          isNew: false,
          isUpdate: false,
        };
        cloneList[index] = findObj;
        setDocumentsList(cloneList);
      }
      // dispatch(getShipmentDocumentsListInSuccess({ data }));
      dispatch(setAppLoader(false));
      triggerNotification(notificationAPI, {
        type: NOTIFICATION_TYPES.SUCCESS,
        title: notificationTitle,
      });

      dispatch(setAppLoader(false));
    } catch (error) {
      dispatch(setAppLoader(false));
      console.log(error);
      triggerNotification(notificationAPI, {
        type: NOTIFICATION_TYPES.ERROR,
        title: t(
          "translation:default.documentManager.notificationUploadingError"
        ),
      });
    }
  };

  const handleSave = async (formData, isNew, index) => {
    dispatch(setAppLoader(true));

    try {
      formData.append("CustomerKey", props.data.customerKey);
      formData.append("ShipmentKey", props.data.shipmentkey);

      if (isNew) {
        formData.append("CreatedBy", user.data.id);
        await shipmentFileUploadEffect(formData);
        getDynamicDocumentList(
          t("translation:default.documentManager.notificationUploadingSuccess"),
          isNew,
          index
        );
      } else {
        formData.append("CreatedBy", user.data.id);
        await shipmentFileUpdateEffect(formData);
        getDynamicDocumentList(
          t("translation:default.documentManager.notificationUploadingSuccess"),
          isNew,
          index
        );
      }
    } catch (error) {
      let msg = getErrorMessageFromAPI(error);
      dispatch(setAppLoader(false));
      triggerNotification(notificationAPI, {
        type: NOTIFICATION_TYPES.ERROR,
        title: msg,
      });
    }
  };

  const editRecord = (index) => {
    let array = [...documentsList];
    array[index]["isUpdate"] = true;
    setDocumentsList(array);
  };

  const handleDelete = async (index, isNew) => {
    try {
      let data = _.cloneDeep(documentsList);
      if (!isNew) {
        dispatch(setAppLoader(true));
        let payload = {
          shipmentKey: props.data.shipmentkey,
          documentFileName: data[index].fileGuid,
        };
        await shipmentFileDeleteEffect(payload);
        dispatch(setAppLoader(false));
        triggerNotification(notificationAPI, {
          type: NOTIFICATION_TYPES.SUCCESS,
          title: t(
            "translation:default.documentManager.notificationDeleteSuccess"
          ),
        });
      }
      data.splice(index, 1);
      setDocumentsList(data);
    } catch (error) {
      dispatch(setAppLoader(false));
      console.log(error);
      triggerNotification(notificationAPI, {
        type: NOTIFICATION_TYPES.ERROR,
        title: t("translation:default.documentManager.notificationDeleteError"),
      });
    }
  };

  const handleReset = (index, isNew) => {
    let data = _.cloneDeep(documentsList);
    if (isNew) {
      dispatch(addNewFileBtn(false));
      let findObj = data.filter((item) => item.isNew != true);
      setDocumentsList(findObj);
    } else {
      let findObj = data[index];
      findObj.isUpdate = false;
      data[index] = findObj;
      setDocumentsList(data);
    }
  };

  return (
    <div className="d-flex flex-direction-column overflow-hidden h-100 br-4">
      <div className="custom-scroll-bar overflow-x-hidden mb-15 flex-1 pl-15 pr-15">
        {documentDetails.progressing == true && (
          <div className="d-flex justify-content-center align-items-center h-100">
            <Spin
              spinning={documentDetails.progressing}
              tip={t("translation:default.loading")}
            ></Spin>
          </div>
        )}
        {documentDetails.progressing === false &&
          documentsList.map((row, index) => (
            <FilesDetailsTabCard
              key={row.renderKey}
              fileDetails={row}
              handleDelete={handleDelete}
              handleSave={handleSave}
              isNew={row.isNew}
              index={index}
              uploadedDocumentTypes={uploadedDocumentTypes}
              allowedExtentions={allowedExtentions}
              editRecord={editRecord}
              handleResetValueAction={(index, isNew) =>
                handleReset(index, isNew)
              }
            />
          ))}
        {documentDetails.progressing == false && documentsList.length == 0 && (
          <div className="d-flex justify-content-center align-items-center h-100">
            {t("translation:default.notification.noRecondfound")}
          </div>
        )}
      </div>
    </div>
  );
}

export default FileDetailsTab;
