import React, { Fragment, useEffect, useMemo, useState } from "react";
import WithFilter from "../../screens/common/WithFilter";
import VisibilityHeader from "../../screens/VisibilityScreen/components/VisibilityHeader";
import VisibilityFooter from "../../screens/VisibilityScreen/components/VisibilityFooter";
import VisibilityFilter from "../../screens/VisibilityScreen/VisibilityFilter";
import VisibilityContent from "../../screens/VisibilityScreen/VisibilityContent";
import { Spin } from "antd";
import { useDispatch, useSelector } from "react-redux";
import EmptyRecords from "../../components/Common/EmptyRecords";
import {
  getShipmentType,
  setShipmentDetails,
} from "../../../redux/shipment/ShipmentActions";
import {
  clearSelectedVisibility,
  getSearchVisibilityListClear,
  getVisibilityExceptionClear,
} from "../../../redux/visibility/VisibilityActions";
import { useLocation } from "react-router-dom";
import { routeFileSelector } from "../../../utilz/i18n";
import { useTranslation } from "react-i18next";

const VisibilityPage = () => {
  const [viewType, setViewType] = useState("CARD");
  const { visibilitySearchList } = useSelector((state) => state.visibility);
  const { selectedCompany } = useSelector((state) => state.auth);
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);

  const dispatch = useDispatch();

  const viewCardType = (type) => {
    if (type === "CARD") {
      setViewType("MAP");
    } else {
      setViewType("CARD");
    }
  };

  useEffect(() => {
    dispatch(getSearchVisibilityListClear());
    dispatch(setShipmentDetails([]));
    dispatch(getShipmentType());
    dispatch(getVisibilityExceptionClear());
    dispatch(clearSelectedVisibility({}));
  }, []);

  useEffect(() => {
    dispatch(getSearchVisibilityListClear());
    dispatch(getVisibilityExceptionClear());
    dispatch(clearSelectedVisibility({}));
  }, [selectedCompany]);

  return (
    <WithFilter
      headerContent={
        <VisibilityHeader viewCardType={viewCardType} viewType={viewType} />
      }
      filterTitle={t('translation:default.buttons.search')}
      filterElement={<VisibilityFilter />}
      contentElement={
        <Fragment>
          {visibilitySearchList.progressing == true &&
            visibilitySearchList.data.length === 0 && (
              <Spin className="d-flex align-items-center h-100 justify-content-center" />
            )}
          {visibilitySearchList.progressing == false &&
            visibilitySearchList.data.length === 0 && <EmptyRecords />}
          <VisibilityContent data={visibilitySearchList.data || []} />
        </Fragment>
      }
      pageTitle={t('translation:default.navMenu.shipmentTracking')}
      footerContent={
        <VisibilityFooter visibilitySearchList={visibilitySearchList} />
      }
    />
  );
};

export default VisibilityPage;
