import { Col, Form, Row, Spin, notification } from "antd";
import _, { cloneDeep, omitBy } from "lodash";
import React, { Fragment, useEffect, useMemo, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { VISIBILITY_EXPORT_FILEDS } from "../../../../contants/VisibilityEnums";
import { getVisibilityExcelData } from "../../../../redux/visibility/VisibilityActions";
import { exportShipmentListToExcel } from "../../../../utilz/exportToExcel";
import FilterCard from "../../../components/Cards/FilterCard";
import FormCheckBox from "../../../components/Inputs/FormCheckBox";
import { useLocation } from "react-router-dom";
import { routeFileSelector } from "../../../../utilz/i18n";
import { useTranslation } from "react-i18next";

const VisibilityExportForm = ({ form }) => {
  const [visibilityInfoAll, setVisibilityInfoAll] = useState(true);
  const { visibilitySearchList, excelData } = useSelector(
    (state) => state.visibility
  );
  const visibilityFileds = [...VISIBILITY_EXPORT_FILEDS()];
  const { selectedCompany } = useSelector((state) => state.auth);
  const {
    user: { data: userDetails },
  } = useSelector((state) => state?.auth);
  const dispatch = useDispatch();
  const [selectedObjVal, setSelectedObjVal] = useState({});
  const [api, contextHolder] = notification.useNotification();
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);
  useEffect(() => {
    if (form) {
      handleSelectAll(visibilityFileds, true);
    }
  }, [form]);

  const handleSelectAll = (data, value) => {
    data.forEach(({ name }) => {
      form.setFieldValue(name, value);
    });
  };

  useEffect(() => {
    if (excelData.progressing === false && excelData.success === true) {
      generateExportValuesWithFields(selectedObjVal, excelData.data);
    } else if (excelData.progressing === false && excelData.error === true) {
      api["error"]({
        message: t('translation:visibility.header.exportForm.exportExcel'),
        description: excelData.data?.message,
      });
    }
  }, [excelData]);

  const handleSubmit = (data) => {
    let req = {
      ...visibilitySearchList.req,
      userType: userDetails?.userType,
      customerKey:
        userDetails?.userType === "CUST" ? `${selectedCompany.companyId}` : "",
      carrierKey:
        userDetails?.userType === "CARR" ? `${selectedCompany.companyId}` : "",
      vendorKey:
        userDetails?.userType === "VEND" ? `${selectedCompany.companyId}` : "",
      pageIndex: 1,
      pageSize: visibilitySearchList?.count?.totalCount || 10,
    };
    delete req.pageIndex;
    dispatch(getVisibilityExcelData(req));

    let selectedFields = omitBy(data, (val) => !val);
    let selectedObj = visibilityFileds.filter(
      (item) => selectedFields[item.name] === true
    );
    setSelectedObjVal(cloneDeep(selectedObj));
  };

  const generateExportValuesWithFields = (columns, data) => {
    let rows = [];
    data.forEach((row) => {
      let arr = [];
      columns.forEach(({ name }) => {
        if (Object.hasOwnProperty.call(row, name)) {
          arr.push(row[name]);
        } else {
          arr.push("");
        }
      });

      rows.push(arr);
    });
    columns = columns.map(({ label }) => ({
      name: label.length > 0 ? label : "---",
    }));
    exportShipmentListToExcel(columns, rows);
  };

  return (
    <Fragment>
      {contextHolder}
      {excelData.progressing === true && (
        <div className="d-flex justify-content-center align-items-center h-100">
          <Spin spinning={excelData.progressing} tip={t('translation:default.loading')}></Spin>
        </div>
      )}
      <Form
        onFinish={handleSubmit}
        className="h-100 custom-scroll-bar"
        form={form}
      >
        <FilterCard
          showBorder={true}
          title={
            <FormCheckBox
              checkBoxClass={"normal-checkbox"}
              label={t('translation:visibility.header.exportForm.title')}
              className={"m-0"}
              checked={visibilityInfoAll}
              onChange={(e) => {
                setVisibilityInfoAll(e.target.checked);
                handleSelectAll(visibilityFileds, e.target.checked);
              }}
            />
          }
          className={"mb-20"}
        >
          <Row className={"px-16 pt-15"}>
            {visibilityFileds.map((item, index) => (
              <Fragment key={index}>
                <Col lg={6}>
                  <Col lg={24}>
                    <FormCheckBox
                      checkBoxClass={"normal-checkbox"}
                      label={item.label}
                      name={item.name}
                      className={"mb-15"}
                    />
                  </Col>
                </Col>
              </Fragment>
            ))}
          </Row>
        </FilterCard>
      </Form>
    </Fragment>
  );
};

export default VisibilityExportForm;
