import { Fragment, useEffect, useMemo, useState } from "react";
import VisibilityMiniCard from "./components/VisibilityMiniCard";
import { Col, Row, Spin } from "antd";
import ScrollPagination from "../../components/Paginations/ScrollPagination";
import VisibilityDetailCardContainer from "./components/VisibilityDetailCardContainer";
import {
  getShipmentDetailsInProgress,
  setShipmentData,
} from "../../../redux/shipment/ShipmentActions";
import { useDispatch, useSelector } from "react-redux";
import {
  addNewTrackingStatusBtn,
  clearSelectedVisibility,
  getSearchVisibilityList,
  setSelectedVisibility,
} from "../../../redux/visibility/VisibilityActions";
import { useLocation } from "react-router-dom";
import { routeFileSelector } from "../../../utilz/i18n";
import { useTranslation } from "react-i18next";

const VisibilityContent = (props) => {
  const dispatch = useDispatch();
  const [selectedShipmentItem, setSelectedShipmentItem] = useState("");
  const { shipmentDetails } = useSelector((state) => state.shipment);
  const { visibilitySearchList } = useSelector((state) => state.visibility);
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);

  useEffect(() => {
    if (props.data.length > 0) {
      setSelectedShipmentItem(props.data[0]);
      if (
        shipmentDetails.data.length === 0 &&
        shipmentDetails.progressing === false
      ) {
        getShipmentDetailsMap(props.data[0]);
      }
    }
  }, [props.data]);

  useEffect(() => {
    if (
      shipmentDetails.progressing === false &&
      Object.keys(shipmentDetails?.data).length > 0
    ) {
      dispatch(clearSelectedVisibility({}));
      dispatch(
        setSelectedVisibility({
          ...selectedShipmentItem,
          ...shipmentDetails?.data,
        })
      );
    }
  }, [shipmentDetails]);

  const getShipmentDetailsMap = (data) => {
    dispatch(setShipmentData({}));
    const payload = {
      CustomerKey: data.customerKey,
      ShipmentKey: data.shipmentkey,
    };
    dispatch(getShipmentDetailsInProgress(payload));
  };

  const handleLoadMore = () => {
    let req = {
      pageIndex: visibilitySearchList.req.pageIndex + 1,
    };
    dispatch(getSearchVisibilityList(req));
  };

  return (
    <Fragment>
      <Row gutter={15} className={"h-100 overflow-y-inherit mt-15"}>
        <Col
          sm={8}
          className={
            "h-100 pb-15 overflow-y-inherit d-flex flex-direction-column"
          }
        >
          <div className="h-100 overflow-y-inherit d-flex flex-direction-column">
            <div className="d-flex flex-direction-column overflow-hidden h-100 br-4">
              <ScrollPagination
                className={`${
                  props.data?.length > 0 ? "mb-15" : "pb-15"
                } h-100`}
                loadMore={handleLoadMore}
                hasMore={
                  visibilitySearchList?.progressing
                    ? false
                    : visibilitySearchList.data?.length <
                      visibilitySearchList?.count?.totalCount
                }
                isLoading={
                  visibilitySearchList.progressing === true &&
                  props.data.length > 0
                } //
                scrollOffset={100}
                isVirtualizationApproach={false}
              >
                {props.data.map((item, index) => (
                  <VisibilityMiniCard
                    key={index}
                    data={item}
                    keyIndex={index}
                    selectedShipmentKey={selectedShipmentItem?.shipmentkey}
                    handleSelectedItem={(data) => {
                      dispatch(addNewTrackingStatusBtn(false));
                      setSelectedShipmentItem(data);
                      getShipmentDetailsMap(data);
                    }}
                  />
                ))}
              </ScrollPagination>
            </div>
          </div>
        </Col>
        <Col
          sm={16}
          className={
            "h-100 pb-15 overflow-y-inherit d-flex flex-direction-column"
          }
        >
          <div className="h-100 overflow-y-inherit d-flex flex-direction-column">
            <div className="d-flex flex-direction-column overflow-hidden h-100 br-4">
              <div className="custom-scroll-bar overflow-x-hidden mb-15 flex-1">
                {props.data.length > 0 && shipmentDetails.data !== false && (
                  <VisibilityDetailCardContainer data={selectedShipmentItem} />
                )}
                {shipmentDetails.progressing === true && (
                  <div className="d-flex justify-content-center align-items-center h-100">
                    <Spin tip={t("translation:default.loading")}></Spin>
                  </div>
                )}
              </div>
            </div>
          </div>
        </Col>
      </Row>
    </Fragment>
  );
};

export default VisibilityContent;
