import { Col, Form, Row, Spin } from "antd";
import React, { useEffect, useMemo, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import Button from "../../../../components/Buttons/Button";
import FiledCard from "../../../../components/Cards/FiledCard";
import PrimaryLabel from "../../../../components/Labels/PrimaryLabel";
import AuctionTrimbleMapAPI from "../../../../components/Maps/AuctionTrimbleMapAPI";
import FormInput from "../../../../components/Inputs/FormInput";
import { updateRefandDockNoInProgress } from "../../../../../redux/visibility/VisibilityActions";
import { useLocation } from "react-router-dom";
import { routeFileSelector } from "../../../../../utilz/i18n";
import { useTranslation } from "react-i18next";

function GeneralTab(props) {
  const [mapFullScreenStatus, setMapFullScreenStatus] = useState(false);
  const dispatch = useDispatch();
  const { updateRedAndDockNoInfo, getShipperConsigneeDetails } = useSelector(
    (state) => state.visibility
  );
  const { shipmentDetails } = useSelector((state) => state.shipment);
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);

  const transportationList = useMemo(
    () =>
      shipmentDetails?.data?.transportationList
        ? shipmentDetails?.data?.transportationList
        : [],
    [shipmentDetails?.data]
  );

  const shipmentCoordinates = useMemo(
    () =>
      transportationList.map(({ latitude, longitude }) => ({
        latitude,
        longitude,
      })),
    [transportationList]
  );

  const setMapViewScreen = () => {
    setMapFullScreenStatus(!mapFullScreenStatus);
  };

  const getProjectList = (data) => {
    let req = {
      Shipmentkey: shipmentDetails?.data?.shipmentKey || "",
      ShipperReferenceNumber: data?.shipperAppointmentNo || "",
      ConsigneeReferenceNumber: data?.consigneeAppointmentNo || "",
      ShipperDockNo: data?.shipperDockNo || "",
      ConsingneeDockNo: data?.consigneeDockNo || "",
    };
    dispatch(updateRefandDockNoInProgress(req));
  };

  useEffect(() => {
    if (props.editGeneralBtnInfo?.isBtnClicked === true) {
      props.form.setFieldValue(
        "shipperAppointmentNo",
        shipmentDetails.data?.shipmentOriginDetails?.[0]?.shipperReferenceNo ||
          ""
      );
      props.form.setFieldValue(
        "shipperDockNo",
        shipmentDetails.data?.shipmentOriginDetails?.[0]?.dockNumber || ""
      );
      props.form.setFieldValue(
        "consigneeAppointmentNo",
        shipmentDetails.data?.shipmentDestinationDetails?.[0]
          ?.consigneeReferenceNo || ""
      );
      props.form.setFieldValue(
        "consigneeDockNo",
        shipmentDetails.data?.shipmentDestinationDetails?.[0]?.dockNumber || ""
      );
    }
  }, [props.editGeneralBtnInfo?.isBtnClicked]);

  return (
    <Row className="h-100">
      {mapFullScreenStatus === false && (
        <Col
          sm={24}
          className={
            "h-100 pb-15 overflow-y-inherit d-flex flex-direction-column"
          }
        >
          <div
            className={` h-100 overflow-y-inherit d-flex flex-direction-column`}
          >
            <div className="d-flex flex-direction-column overflow-hidden h-100 br-4">
              <div className="custom-scroll-bar overflow-x-hidden mb-15 flex-1">
                {updateRedAndDockNoInfo.progressing === true && (
                  <div className="d-flex justify-content-center align-items-center h-100">
                    <Spin tip={t('translation:default.loading')}></Spin>
                  </div>
                )}
                <Form form={props.form} onFinish={getProjectList}>
                  <Row className="pl-15">
                    <Col sm={8}>
                      <Row className="mb-15">
                        <Col sm={24}>
                          <div className="d-flex align-items-center w-100">
                            <div className="w-40 font-size-12 font-weight-400"> {t('translation:visibility.detailCard.generalTab.shipmentDetails')}
                            </div>
                            <div className="w-60 ml-15">
                              <hr></hr>
                            </div>
                          </div>
                        </Col>
                      </Row>

                      <Row className="mb-15">
                        <Col sm={12}>
                          <FiledCard title={t('translation:default.fields.customerName.label')}>
                            <PrimaryLabel className="text-highlight font-weight-600">
                              {props.data.customerName}
                            </PrimaryLabel>
                          </FiledCard>
                        </Col>
                        <Col sm={12}>
                          <FiledCard title={t('translation:default.fields.branch.label')}>
                            <PrimaryLabel normal={true}>
                              {props.data.branchDesc}
                            </PrimaryLabel>
                          </FiledCard>
                        </Col>
                      </Row>
                      <Row className="mb-15">
                        <Col sm={12}>
                          <FiledCard title={t('translation:visibility.detailCard.generalTab.shipmentMode')}>
                            <PrimaryLabel normal={true}>
                              {props.data.shipmentMode}
                            </PrimaryLabel>
                          </FiledCard>
                        </Col>
                        <Col sm={12}>
                          <FiledCard title={t('translation:visibility.detailCard.generalTab.multiShop')}>
                            <PrimaryLabel normal={true}>
                              {props.data.multiStop}
                            </PrimaryLabel>
                          </FiledCard>
                        </Col>
                      </Row>

                      <Row className="mb-15">
                        <Col sm={24} className="font-size-12 font-weight-400">
                         {t('translation:default.fields.transportationProvider.label')}
                        </Col>
                        <Col sm={24}>
                          <PrimaryLabel normal={true}>
                            {props.data.transportationProvider}
                          </PrimaryLabel>
                        </Col>
                      </Row>

                      {/* <Row className="mb-15">
                        <Col sm={12}>
                          <FiledCard title={t('translation:default.fields.shipmentCost') }>
                            <PrimaryLabel normal={true}>
                              {shipmentDetails.data.shipmentCost}
                            </PrimaryLabel>
                          </FiledCard>
                        </Col>
                        <Col sm={12}>
                          <FiledCard title={t('translation:default.fields.currency.label')}>
                            <PrimaryLabel normal={true}>
                              {props.data.currencyCode}
                            </PrimaryLabel>
                          </FiledCard>
                        </Col>
                      </Row> */}

                      <Row className="mb-15">
                        <Col sm={24}>
                          <div className="d-flex align-items-center w-100">
                            <div className="w-40 font-size-12 font-weight-400">
                             {t('translation:visibility.detailCard.generalTab.shipperDetails')}
                            </div>
                            <div className="w-60 ml-15">
                              <hr></hr>
                            </div>
                          </div>
                        </Col>
                      </Row>

                      <Row className="mb-15">
                        <Col sm={24}> {t('translation:visibility.detailCard.generalTab.shipperContact')}</Col>
                        <Col sm={24}>
                          <PrimaryLabel normal={true}>
                            {
                              getShipperConsigneeDetails?.data?.shipperData?.[0]
                                ?.contactName
                            }
                            <div>
                              {
                                shipmentDetails.data?.shipmentOriginDetails?.[0]
                                  ?.address1
                              }
                            </div>
                            <div>
                              {
                                shipmentDetails.data?.shipmentOriginDetails?.[0]
                                  ?.address2
                              }
                            </div>
                          </PrimaryLabel>
                          <div>
                            {`${t('translation:default.fields.phone')}: `}
                            {
                              shipmentDetails.data?.shipmentOriginDetails?.[0]
                                ?.shipperBusinessPhone
                            }
                          </div>
                          <div>
                             {t('translation:default.fields.email')}{" "}
                            {
                              shipmentDetails.data?.shipmentOriginDetails?.[0]
                                ?.originAdditionalInfo?.contactEmail
                            }
                          </div>
                        </Col>
                      </Row>

                      <Row className="mb-15" gutter={16}>
                        <Col sm={12}>
                          {props.editGeneralBtnInfo?.isBtnClicked === true && (
                              <FormInput
                                editMode={true}
                                label={t('translation:visibility.detailCard.generalTab.appointmentNo')}
                                placeholder={t('translation:visibility.detailCard.generalTab.appointmentNo')}
                                name="shipperAppointmentNo"
                                value={
                                  shipmentDetails.data
                                    ?.shipmentOriginDetails?.[0]
                                    ?.shipperReferenceNo
                                }
                              />
                            )}
                          {props.editGeneralBtnInfo?.isBtnClicked === false && (
                            <FiledCard title={t('translation:visibility.detailCard.generalTab.appointmentNo')}>
                              <PrimaryLabel normal={true}>
                                {
                                  shipmentDetails.data
                                    ?.shipmentOriginDetails?.[0]
                                    ?.shipperReferenceNo
                                }
                              </PrimaryLabel>
                            </FiledCard>
                          )}
                        </Col>
                        <Col sm={12}>
                          {props.editGeneralBtnInfo?.isBtnClicked === true &&
                            props.userDetails?.userType === "CUST" && (
                              <FormInput
                                editMode={true}
                                label={t('translation:visibility.detailCard.generalTab.dockNo')}
                                placeholder={t('translation:visibility.detailCard.generalTab.dockNo')}
                                name="shipperDockNo"
                                value={
                                  shipmentDetails.data
                                    ?.shipmentOriginDetails?.[0]?.dockNumber
                                }
                                maxLength={100}
                              />
                            )}
                          {props.editGeneralBtnInfo?.isBtnClicked === false && (
                            <FiledCard title={t('translation:visibility.detailCard.generalTab.dockNo')}>
                              <PrimaryLabel normal={true}>
                                {
                                  shipmentDetails.data
                                    ?.shipmentOriginDetails?.[0]?.dockNumber
                                }
                              </PrimaryLabel>
                            </FiledCard>
                          )}
                        </Col>
                      </Row>
                      <Row className="mb-15">
                        <Col sm={24}>
                          <div className="d-flex align-items-center w-100">
                            <div className="w-40 font-size-12 font-weight-400">
                             {t('translation:visibility.detailCard.generalTab.consigneeDetails')}
                            </div>
                            <div className="w-60 ml-15">
                              <hr></hr>
                            </div>
                          </div>
                        </Col>
                      </Row>

                      <Row className="mb-15">
                        <Col sm={24}> {t('translation:visibility.detailCard.generalTab.consigneeContact')}</Col>
                        <Col sm={24}>
                          <PrimaryLabel normal={true}>
                            {
                              getShipperConsigneeDetails?.data
                                ?.consigneeData?.[0]?.contactName
                            }
                          </PrimaryLabel>
                          <div>
                                {`${t('translation:default.fields.phone')}: `}
                            {
                              shipmentDetails.data
                                ?.shipmentDestinationDetails?.[0]
                                ?.consigneeBusinessPhone
                            }{" "}
                          </div>
                          <div>
                            {`${t('translation:default.fields.email')}: `}
                            {
                              shipmentDetails.data
                                ?.shipmentDestinationDetails?.[0]
                                ?.destinationAdditionalInfo?.contactEmail
                            }{" "}
                          </div>
                        </Col>
                      </Row>
                      <Row className="mb-15" gutter={16}>
                        <Col sm={12}>
                          {props.editGeneralBtnInfo?.isBtnClicked === true &&
                            props.userDetails?.userType === "CUST" && (
                              <FormInput
                                editMode={true}
                                label={t('translation:visibility.detailCard.generalTab.appointmentNo')}
                                placeholder={t('translation:visibility.detailCard.generalTab.appointmentNo')}
                                name="consigneeAppointmentNo"
                                value={
                                  shipmentDetails.data
                                    ?.shipmentDestinationDetails?.[0]
                                    ?.consigneeAppointmentType
                                }
                              />
                            )}
                          {props.editGeneralBtnInfo?.isBtnClicked === false && (
                            <FiledCard title={t('translation:visibility.detailCard.generalTab.appointmentNo')}>
                              <PrimaryLabel normal={true}>
                                {
                                  shipmentDetails.data
                                    ?.shipmentDestinationDetails?.[0]
                                    ?.consigneeReferenceNo
                                }
                              </PrimaryLabel>
                            </FiledCard>
                          )}
                        </Col>
                        <Col sm={12}>
                          {props.editGeneralBtnInfo?.isBtnClicked === true &&
                            props.userDetails?.userType === "CUST" && (
                              <FormInput
                                editMode={true}
                                label={t('translation:visibility.detailCard.generalTab.dockNo')}
                                placeholder={t('translation:visibility.detailCard.generalTab.dockNo')}
                                name="consigneeDockNo"
                                value={
                                  shipmentDetails.data
                                    ?.shipmentDestinationDetails?.[0]
                                    ?.dockNumber
                                }
                              />
                            )}
                          {props.editGeneralBtnInfo?.isBtnClicked === false && (
                            <FiledCard title={t('translation:visibility.detailCard.generalTab.dockNo')}>
                              <PrimaryLabel normal={true}>
                                {
                                  shipmentDetails.data
                                    ?.shipmentDestinationDetails?.[0]
                                    ?.dockNumber
                                }
                              </PrimaryLabel>
                            </FiledCard>
                          )}
                        </Col>
                      </Row>
                    </Col>

                    <Col sm={16} className="pl-15 pr-15">
                      <Row className="h-42 mb-15">
                        <Col sm={24}>
                          {shipmentCoordinates?.length > 0 && (
                            <AuctionTrimbleMapAPI
                              handleFullScreenClick={() => setMapViewScreen()}
                              hasEnabledFullScreen={mapFullScreenStatus}
                              coordinates={shipmentCoordinates}
                              transportationList={transportationList}
                              drawLineOnMap={false}
                            />
                          )}
                          {shipmentCoordinates.length === 0 && (
                            <div className="d-flex justify-content-center align-items-center h-100">
                              <Spin
                                spinning={
                                  shipmentCoordinates.length === 0
                                    ? true
                                    : false
                                }
                                delay={100}
                                tip={t('translation:default.loading')}
                              ></Spin>
                            </div>
                          )}
                        </Col>
                      </Row>

                      <Row className="mb-15">
                        <Col sm={24}>
                          <div className="d-flex align-items-center w-100">
                            <div className="w-25 font-size-12 font-weight-400">
                              {t('translation:visibility.detailCard.generalTab.shipmentLocation')}
                            </div>
                            <div className="w-75 ml-15">
                              <hr></hr>
                            </div>
                          </div>
                        </Col>
                      </Row>
                      <Row className="mb-15">
                        <Col sm={8}>
                          <FiledCard title={t('translation:default.fields.country.label')}>
                            <PrimaryLabel normal={true}>
                              {
                                shipmentDetails.data?.shipmentOriginDetails?.[0]
                                  ?.countryName
                              }
                            </PrimaryLabel>
                          </FiledCard>
                        </Col>
                        <Col sm={8}>
                          <FiledCard title={t('translation:default.fields.stateProvince.label')}>
                            <PrimaryLabel normal={true}>
                              {
                                shipmentDetails.data?.shipmentOriginDetails?.[0]
                                  ?.stateName
                              }
                            </PrimaryLabel>
                          </FiledCard>
                        </Col>
                        <Col sm={8}>
                          <FiledCard title={t('translation:default.fields.city.label')}>
                            <PrimaryLabel normal={true}>
                              {
                                shipmentDetails.data?.shipmentOriginDetails?.[0]
                                  ?.city
                              }
                            </PrimaryLabel>
                          </FiledCard>
                        </Col>
                      </Row>

                      <Row className="mb-15">
                        <Col sm={24}>
                          <div className="d-flex align-items-center w-100">
                            <div className="w-20 font-size-12 font-weight-400">
                              {t('translation:visibility.detailCard.generalTab.trackingDetails')}
                            </div>
                            <div className="w-50 ml-15">
                              <hr></hr>
                            </div>
                            <div className="w-30 d-flex justify-content-center">
                              <Button
                                onClick={() => props.viewMoreStatusClick()}
                                type="primary"
                                className="auction-btn border-radion-5"
                              >
                                {t('translation:default.buttons.viewMoreStatus')}
                              </Button>
                            </div>
                          </div>
                        </Col>
                      </Row>

                      <Row className="mb-15">
                        <Col sm={8}>
                          <FiledCard title={`${t('translation:visibility.trackingStatusInfo.trackingStatus.label')}:`}>
                            <PrimaryLabel className="text-highlight font-weight-600">
                              {props.data.trackingStatus}
                            </PrimaryLabel>
                          </FiledCard>
                        </Col>
                        <Col sm={8}>
                          <FiledCard title={t('translation:visibility.detailCard.generalTab.trackingStatusType')}>
                            <PrimaryLabel normal={true}>
                              {shipmentDetails.data.status}
                            </PrimaryLabel>
                          </FiledCard>
                        </Col>
                        <Col sm={8}>
                          <FiledCard title={t('translation:default.fields.exceptionType.label')}>
                            <PrimaryLabel normal={true}>
                              {props.data.shipmentExceptionTypeName}
                            </PrimaryLabel>
                          </FiledCard>
                        </Col>
                      </Row>

                      <Row className="mb-15">
                        <Col sm={8}>
                          <FiledCard title={t('translation:visibility.detailCard.generalTab.transporterPlannedShipDate')}>
                            <PrimaryLabel normal={true}>
                              {props.data.transProAcShipDate}
                            </PrimaryLabel>
                          </FiledCard>
                        </Col>
                        <Col sm={8}>
                          <FiledCard
                            title={t('translation:visibility.detailCard.generalTab.transporterPlannedDeliveryDate')}
                          >
                            <PrimaryLabel normal={true}>
                              {props.data.transProAcDeliveryDate}
                            </PrimaryLabel>
                          </FiledCard>
                        </Col>
                        <Col sm={8}>
                          <FiledCard title={t('translation:visibility.detailCard.generalTab.eventOccurrence')}>
                            <PrimaryLabel normal={true}>
                              {props.data.eventLocalTime}
                            </PrimaryLabel>
                          </FiledCard>
                        </Col>
                      </Row>

                      <Row className="mb-15">
                        <Col sm={8}>
                          <FiledCard title={t('translation:visibility.detailCard.generalTab.shipperRequestedShipDate')}>
                            <PrimaryLabel normal={true}>
                              {props.data.shipReqShipDate}
                            </PrimaryLabel>
                          </FiledCard>
                        </Col>
                        <Col sm={8}>
                          <FiledCard title={t('translation:default.fields.shipperReqDeliveryDate')}>
                            <PrimaryLabel normal={true}>
                              {props.data.shipReqDeliveryDate}
                            </PrimaryLabel>
                          </FiledCard>
                        </Col>
                        <Col sm={8}>
                          <FiledCard title={t('translation:default.fields.updateReceivedEST')}>
                            <PrimaryLabel normal={true}>
                              {props.data.expectedDeliveryDate}
                            </PrimaryLabel>
                          </FiledCard>
                        </Col>
                      </Row>

                      <Row className="mb-15">
                        <Col sm={8}>
                          <FiledCard title={t('translation:default.fields.createdOn.title')}>
                            <PrimaryLabel normal={true}>
                              {props.data.createdOnDate}
                            </PrimaryLabel>
                          </FiledCard>
                        </Col>
                        <Col sm={8}>
                          <FiledCard title={t('translation:default.fields.submittedBy')}>
                            <PrimaryLabel normal={true}></PrimaryLabel>
                          </FiledCard>
                        </Col>
                        <Col sm={8}>
                          <FiledCard title={t('translation:default.fields.signature')}>
                            <PrimaryLabel normal={true}>
                              {props.data.signature}
                            </PrimaryLabel>
                          </FiledCard>
                        </Col>
                      </Row>
                    </Col>
                  </Row>
                </Form>
              </div>
            </div>
          </div>
        </Col>
      )}
      {mapFullScreenStatus === true && (
        <Col
          sm={24}
          className={
            "h-100 pb-15 overflow-y-inherit d-flex flex-direction-column"
          }
        >
          {shipmentCoordinates?.length > 0 && (
            <AuctionTrimbleMapAPI
              handleFullScreenClick={() => setMapViewScreen()}
              hasEnabledFullScreen={mapFullScreenStatus}
              coordinates={shipmentCoordinates}
              transportationList={transportationList}
              drawLineOnMap={false}
            />
          )}
        </Col>
      )}
    </Row>
  );
}

export default GeneralTab;
