import { Button, Card, Col, Form, Row } from "antd";
import React, { Fragment, useMemo, useState } from "react";
import FiledCard from "../../../../components/Cards/FiledCard";
import Files from "react-files";
import HiddenFormInput from "../../../../components/Inputs/HiddenFormInput";
import DocumentTypeSelector from "../../../../components/Selectors/DocumentTypeSelector";
import { MODULES_ID } from "../../../../../contants/common";
import FormInput from "../../../../components/Inputs/FormInput";
import { ReactComponent as DownloadIcon } from "../../../../../assets/icons/download-icon.svg";
import { ReactComponent as EditIcon } from "../../../../../assets/icons/edit-icon.svg";
import { ReactComponent as CloseIcon } from "../../../../../assets/icons/close-icon.svg";
import { useLocation } from "react-router-dom";
import { routeFileSelector } from "../../../../../utilz/i18n";
import { useTranslation } from "react-i18next";

function FilesDetailsTabCard({
  isNew,
  fileDetails,
  handleSave,
  handleDelete,
  index,
  allowedExtentions,
  editRecord,
  handleResetValueAction = () => {},
}) {
  const [form] = Form.useForm();
  const [file, setFile] = useState(false);
  const fileName = Form.useWatch("file", form);
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);

  const handleFileChange = (files) => {
    try {
      if (files.length > 0) {
        setFile(files[0]);
        form.setFieldsValue({ file: files[0].name });
      }
    } catch (error) {
      console.log(error);
    }
  };

  const handleFileError = (error) => {
    try {
      if (error) {
        form.setFields([
          {
            name: "file",
            errors: [error.message],
          },
        ]);
      }
    } catch (error) {
      console.log(error);
    }
  };

  const handleReset = (index, isNew) => {
    form.setFieldsValue({
      docTypeCode: fileDetails.fileType,
      referenceNo: fileDetails.referenceNo,
      documentTitle: fileDetails.fileComments,
      documentDesc: fileDetails.fileDescription,
    });
    handleResetValueAction(index, isNew);
  };

  const handleFileSubmit = (data) => {
    const formData = new FormData();
    formData.append(
      "DocumentTitle",
      data.documentTitle ? data.documentTitle : ""
    );
    formData.append("ReferenceNo", data.referenceNo ? data.referenceNo : "");
    formData.append(
      "DocumentTypeCode",
      data.docTypeCode ? data.docTypeCode : ""
    );
    formData.append("DocumentDesc", data.documentDesc ? data.documentDesc : "");
    if (file) {
      formData.append("File", file);
    }
    handleSave(formData, isNew, index);
  };

  return (
    <Form
      form={form}
      onFinish={handleFileSubmit}
      initialValues={fileDetails}
      {...{
        initialValues: fileDetails?.fileGuid
          ? {
              docTypeCode: fileDetails.fileType,
              referenceNo: fileDetails.referenceNo,
              documentTitle: fileDetails.fileComments,
              documentDesc: fileDetails.fileDescription,
              file: fileDetails.file,
            }
          : {},
      }}
    >
      <Card
        title={
          <div
            className={`d-flex gap-1 ${
              fileDetails.isUpdate === false ? "align-items-center" : ""
            }`}
          >
            {fileDetails.isUpdate === false && (
              <FormInput
                editMode={false}
                label={t('translation:default.fields.fileName')}
                name="file"
                singleLineMode={true}
                margin="mb-0"
                className={fileDetails.isUpdate ? "input-height" : ""}
              />
            )}

            {fileDetails.isUpdate && allowedExtentions && (
              <Fragment>
                <FiledCard
                  title={t('translation:default.fields.fileName')}
                  textClass={"d-flex align-items-center gap-1"}
                >
                  <Files
                    className="files-dropzone"
                    onChange={handleFileChange}
                    onError={handleFileError}
                    accepts={allowedExtentions}
                    multiple={false}
                    // maxFileSize={10000000}
                    minFileSize={0}
                    clickable
                  >
                    <Button className="auction-btn w-100 border-radion-5 white-font">
                      {t('translation:default.documentManager.chooseFile')}
                    </Button>
                  </Files>

                  <HiddenFormInput
                    name="file"
                    rules={[
                      { required: true, message:  t('translation:default.documentManager.chooseFileMessage') },
                      () => ({
                        validator(_, value) {
                          if(!value){
                            return Promise.resolve();
                          }
                         let extension = value.match(/\.([^.]+)$/)[1];
                          extension = `.${extension}`;
                          if(allowedExtentions.includes(extension)){
                            return Promise.resolve();
                          } else if(!allowedExtentions.includes(extension)){
                                return Promise.reject(
                                  new Error(`${t('translation:default.documentManager.only')} ${allowedExtentions.join(",")} ${t('translation:default.documentManager.filesAreAllowed')} `)
                                );
                          } 
                        },
                      }),
                    ]}
                  />
                </FiledCard>
                <label className="f-14 fw-600">{fileName}</label>
              </Fragment>
            )}
          </div>
        }
        className="mb-15 visibility-file-card"
        extra={
          <div className="d-flex gap-1">
            {fileDetails.isUpdate && (
              <Fragment>
                <Button
                  type="primary"
                  buttonType="submit"
                  onClick={() => form.submit()}
                  className="bg-success bg-success-active white-font w-100 ml-5 mr-5 border-radion-5"
                >
                  {isNew === true ? t('translation:default.buttons.add') : t('translation:default.buttons.update')}
                </Button>
                <Button
                  type="default"
                  danger
                  buttonType="button"
                  className="border-radion-5 w-100"
                  onClick={() => handleReset(index, isNew)}
                >
                  {t('translation:default.buttons.cancel')}
                </Button>
              </Fragment>
            )}
            {!fileDetails.isUpdate && (
              <Fragment>
                <DownloadIcon
                  onClick={() =>
                    window.location.assign(fileDetails?.cloudUrl?.[0])
                  }
                />
                <EditIcon onClick={() => editRecord(index)} />
                <CloseIcon onClick={() => handleDelete(index, isNew)} />
              </Fragment>
            )}
          </div>
        }
      >
        <Row>
          <Col sm={8} className="pr-15">
            <DocumentTypeSelector
              editMode={fileDetails.isUpdate}
              margin={"mb-16"}
              name={"docTypeCode"}
              moduleId={MODULES_ID.VISIBILITY}
              onChange={({ label }) =>
                form.setFieldsValue({ documentDesc: label })
              }
              rules={[
                { required: true, message: t('translation:default.documentManager.documentTypeMessage')}
              ]}
            />
          </Col>
          <Col sm={8} className="pr-15">
            <FormInput
              editMode={fileDetails.isUpdate}
              label={t('translation:default.documentManager.fileComments.label')}
              placeholder={t('translation:default.documentManager.fileComments.placeholder')}
              name="documentTitle"
              className={fileDetails.isUpdate ? "input-height" : ""}
            />
          </Col>
          <Col sm={8}>
            <FormInput
              editMode={fileDetails.isUpdate}
              label={t('translation:default.documentManager.referenceNo.label')}
              placeholder={t('translation:default.documentManager.referenceNo.label')}
              name="referenceNo"
              className={fileDetails.isUpdate ? "input-height" : ""}
              maxLength={50}
            />
          </Col>
        </Row>
      </Card>
      <HiddenFormInput name="documentDesc" />
    </Form>
  );
}
export default FilesDetailsTabCard;
