import { Col, Row, Spin } from "antd";
import React, { useEffect, useMemo, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  getShipmentExceptionTypes,
  getShipmentItrackStatusLookups,
  getShipmentItrackStatusTypes,
} from "../../../../../redux/lookups/LookupsActions";
import {
  addNewTrackingStatusBtn,
  getTrackingStatusDetails,
  updateShipmentTrackingDetailsClear,
} from "../../../../../redux/visibility/VisibilityActions";
import AuctionTrimbleMapAPI from "../../../../components/Maps/AuctionTrimbleMapAPI";
import _ from "lodash";
import { useLocation } from "react-router-dom";
import { routeFileSelector } from "../../../../../utilz/i18n";
import { useTranslation } from "react-i18next";
import { triggerNotification } from "../../../../../utilz/utilz";
import { NOTIFICATION_TYPES } from "../../../../../contants/common";

function TrackingStatusDetails(props) {
  const dispatch = useDispatch();
  const [mapFullScreenStatus, setMapFullScreenStatus] = useState(false);
  const [trackingStatusList, setTrackingStatusList] = useState([]);
  const { trackingStatusDetails } = useSelector((state) => state.visibility);
  const { shipmentDetails } = useSelector((state) => state.shipment);
  const notificationAPI = useSelector((state) => state.common.notificationAPI);

  const { updateShipmentTrackingDetailsData, addNewStatusAction } = useSelector(
    (state) => state.visibility
  );
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);
  const setMapViewScreen = () => {
    setMapFullScreenStatus(!mapFullScreenStatus);
  };

  useEffect(() => {
    if (props.data.shipmentNo) {
      dispatch(
        getTrackingStatusDetails({ shipmentNumber: props.data.shipmentNo })
      ); //props.data.shipmentNo
    }
  }, [props.data.shipmentNo]);

  useEffect(() => {
    dispatch(getShipmentExceptionTypes());
    dispatch(getShipmentItrackStatusLookups());
    dispatch(getShipmentItrackStatusTypes());
  }, []);

  useEffect(() => {
    if (updateShipmentTrackingDetailsData?.data == "Success") {
      dispatch(updateShipmentTrackingDetailsClear());
      dispatch(addNewTrackingStatusBtn(false));
      dispatch(
        getTrackingStatusDetails({ shipmentNumber: props.data.shipmentNo })
      );
    }
    if(updateShipmentTrackingDetailsData?.data === 'Error'){
      triggerNotification(notificationAPI, {
        type: NOTIFICATION_TYPES.ERROR,
        title: 'Tracking Status Update error.',
      });
      dispatch(updateShipmentTrackingDetailsClear());
    }
  }, [updateShipmentTrackingDetailsData]);

  useEffect(() => {
    setTrackingStatusList(
      trackingStatusDetails.data.map((row) => ({
        ...row,
        renderKey: _.uniqueId(),
        isNew: false,
        isUpdate: false,
      }))
    );
  }, [trackingStatusDetails.data]);

  const handleNewStatus = () => {
    let data = _.cloneDeep(trackingStatusList);
    data = [{ renderKey: _.uniqueId(), isNew: true, isUpdate: true }, ...data];
    setTrackingStatusList(data);
  };

  useEffect(() => {
    if (addNewStatusAction.btnCliked) {
      handleNewStatus();
    }
  }, [addNewStatusAction]);

  const transportationList = useMemo(
    () =>
      shipmentDetails?.data?.transportationList
        ? shipmentDetails?.data?.transportationList
        : [],
    [shipmentDetails?.data]
  );

  const shipmentCoordinates = useMemo(
    () =>
      transportationList.map(({ latitude, longitude }) => ({
        latitude,
        longitude,
      })),
    [transportationList]
  );

  return (
    <Row className="h-100">
      <Col
        sm={24}
        className={
          "h-100 pb-15 overflow-y-inherit d-flex flex-direction-column"
        }
      >
        <div
          className={`h-100 overflow-y-inherit d-flex flex-direction-column`}
        >
          <div className="d-flex flex-direction-column overflow-hidden h-100 br-4">
            <div className="custom-scroll-bar overflow-hidden pl-15 pr-15 pb-15 flex-1">
              {trackingStatusDetails.progressing == true && (
                <div className="d-flex justify-content-center align-items-center h-100">
                  <Spin
                    spinning={trackingStatusDetails.progressing}
                    tip={t('translation:default.loading')}
                  ></Spin>
                </div>
              )}
              {trackingStatusDetails.progressing === false && (
                <AuctionTrimbleMapAPI
                  handleFullScreenClick={() => setMapViewScreen()}
                  hasEnabledFullScreen={mapFullScreenStatus}
                  coordinates={shipmentCoordinates}
                  transportationList={transportationList}
                  drawLineOnMap={false}
                  trackingStatusInfo={true}
                  trackingStatusData={trackingStatusList || []}
                  visibilityRecord={{ ...props.data }}
                  mapId="visibility-tracking-trimble-map"
                  removeNewRecord={() => {
                    let copytrackingStatusList =
                      _.cloneDeep(trackingStatusList);
                    copytrackingStatusList.splice(0, 1);
                    setTrackingStatusList(copytrackingStatusList);
                  }}
                />
              )}
            </div>
          </div>
        </div>
      </Col>
    </Row>
  );
}

export default TrackingStatusDetails;
