import React, { useEffect, useState } from "react";
import FormSelectBox from "../Inputs/FormSelectBox";
import { getVisibilityBranchsEffects } from "../../../redux/visibility/VisibilityEffects";
import i18n from 'i18next';

const VisibilityBranchSelector = ({
  placeholder = i18n.t('translation:default.fields.shipmentBranch.placeholder'),
  label = i18n.t('translation:default.fields.shipmentBranch.label'),
  rules = [],
  name,
  form = {},
  customerKey,
  margin,
  editMode = true,
  mode = "",
}) => {
  const [options, setOptions] = useState([]);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    if (customerKey) {
      getStatusList();
    }
  }, [customerKey]);

  const getStatusList = async () => {
    try {
      setLoading(true);
      const { data } = await getVisibilityBranchsEffects({ customerKey });
      let options = data.map((row) => ({
        label: row.branchDesc,
        value: row.branchKey,
      }));

      setOptions(options);
      setLoading(false);
    } catch (error) {
      console.log(error);
      setOptions([]);
      setLoading(false);
    }
  };

  return (
    <FormSelectBox
      loading={loading}
      options={options}
      placeholder={placeholder}
      label={label}
      margin={margin}
      rules={rules}
      name={name}
      editMode={editMode}
      form={form}
      mode={mode}
    />
  );
};

export default VisibilityBranchSelector;
