import { Card, Col, Form, Row } from "antd";
import React, { Fragment, useMemo, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import FiledCard from "../../../../components/Cards/FiledCard";
import PrimaryLabel from "../../../../components/Labels/PrimaryLabel";
import SecondaryLabel from "../../../../components/Labels/SecondaryLabel";
import SaveCancelButton from "../../../../components/Common/SaveCancelButton";
import FormTextArea from "../../../../components/Inputs/FormTextArea";
import { getLabelFromDate } from "../../../../../utilz/dates";
import { updateShipmentNoteInProgress } from "../../../../../redux/visibility/VisibilityActions";
import { useLocation } from "react-router-dom";
import { routeFileSelector } from "../../../../../utilz/i18n";
import { useTranslation } from "react-i18next";

function VisibilityShipmentNoteTab(props) {
  const dispatch = useDispatch();
  const [form] = Form.useForm();
  const { getShipmentNotesetails } = useSelector((state) => state.visibility);
  const [state, setState] = useState({
    isEdit: false,
  });
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);
  const handleSubmit = () => {
    let shipmentNoteTxt = form.getFieldValue("ShipmentNotes");
    let req = {
      ShipmentNotes: shipmentNoteTxt || "",
      TrackingNumber: props?.data?.shipmentNo,
      shipmentkey: props?.data?.shipmentkey,
    };
    dispatch(updateShipmentNoteInProgress(req));
  };
  return (
    <div className="d-flex flex-direction-column overflow-hidden h-100 br-4 visibility-product-info-tab">
      <div className="custom-scroll-bar overflow-x-hidden mb-15 flex-1 pl-15 pr-15">
        <Form
          onFinish={() => {}}
          form={form}
          initialValues={{
            ShipmentNotes: getShipmentNotesetails?.data?.shipmentNotes,
          }}
        >
          <Card
            className={`mb-15 shipment-note-tab`}
            title={
              <Fragment>
                <Row gutter={12}>
                  <Col sm={8}>
                    <FiledCard
                      title={t(
                        "translation:visibility.detailCard.notesTab.createdBy"
                      )}
                    >
                      <PrimaryLabel normal={true} className="font-weight-400">
                        {getShipmentNotesetails?.data?.createdBy}
                      </PrimaryLabel>
                    </FiledCard>
                  </Col>
                  <Col sm={4}>
                    <FiledCard
                      title={t(
                        "translation:visibility.detailCard.notesTab.createdOn"
                      )}
                    >
                      <PrimaryLabel normal={true} className="font-weight-400">
                        {getLabelFromDate(
                          getShipmentNotesetails?.data?.createdOn
                        )}
                      </PrimaryLabel>
                    </FiledCard>
                  </Col>

                  <Col sm={8}>
                    <FiledCard
                      title={t(
                        "translation:visibility.detailCard.notesTab.editedBy"
                      )}
                    >
                      <PrimaryLabel normal={true} className="font-weight-400">
                        {getShipmentNotesetails?.data?.modifiedBy}
                      </PrimaryLabel>
                    </FiledCard>
                  </Col>

                  <Col sm={4}>
                    <FiledCard
                      title={t(
                        "translation:visibility.detailCard.notesTab.editedOn"
                      )}
                    >
                      <PrimaryLabel normal={true} className="font-weight-400">
                        {getLabelFromDate(
                          getShipmentNotesetails?.data?.modifiedOn
                        )}
                      </PrimaryLabel>
                    </FiledCard>
                  </Col>
                </Row>
              </Fragment>
            }
            extra={
              <div className="pl-5">
                <SaveCancelButton
                  hideDeleteButton={true}
                  isNew={false}
                  editMode={state.isEdit}
                  setEditMode={() =>
                    setState((state) => ({
                      ...state,
                      isEdit: true,
                    }))
                  }
                  onCancel={() =>
                    setState((state) => ({
                      ...state,
                      isEdit: false,
                    }))
                  }
                  handleRemove={() =>
                    setState((state) => ({
                      ...state,
                      isEdit: false,
                    }))
                  }
                  onSave={() => handleSubmit()}
                />
              </div>
            }
          >
            <Row>
              <Col sm={24}>
                {state.isEdit === true && (
                  <FormTextArea
                    editMode={true}
                    name={"ShipmentNotes"}
                    placeholder={t("translation:default.fields.shipmentNotes")}
                    enableAutoFocus={false}
                    rows={5}
                    defaultValue={getShipmentNotesetails?.data?.shipmentNotes}
                    maxLength={2000}
                  />
                )}
                {state.isEdit === false && (
                  <SecondaryLabel className={"f-14 fw-400"}>
                    {getShipmentNotesetails?.data?.shipmentNotes}
                  </SecondaryLabel>
                )}
              </Col>
            </Row>
          </Card>
        </Form>
      </div>
    </div>
  );
}
export default VisibilityShipmentNoteTab;
