import { Button, Col, Row, Spin, Form } from "antd";
import React, { Fragment, useState, useMemo } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  NOTIFICATION_TYPES,
  SHIPMENT_TRACK_OPTIONS,
} from "../../../../../contants/common";
import { SHIPMENT_TRACKING_TYPES } from "../../../../../contants/ShipmentEnums";
import { triggerNotification } from "../../../../../utilz/utilz";
import FiledCard from "../../../../components/Cards/FiledCard";
import FormInput from "../../../../components/Inputs/FormInput";
import PrimaryLabel from "../../../../components/Labels/PrimaryLabel";
import ScrollPagination from "../../../../components/Paginations/ScrollPagination";
import { useLocation } from "react-router-dom";
import { routeFileSelector } from "../../../../../utilz/i18n";
import { useTranslation } from "react-i18next";
import { updateShipmentDetailsInProgress } from "../../../../../redux/visibility/VisibilityActions";

function VisibilityDetailsTab(props) {
  const dispatch = useDispatch();
  const [form] = Form.useForm();
  const { getShipmentNotesetails } = useSelector((state) => state.visibility);
  const notificationAPI = useSelector((state) => state.common.notificationAPI);
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);
  const [selectedNumber, setSelectedNumber] = useState(undefined);

  const updateShipmentDetails = (data) => {
    let req = {
      bolNumber: data?.bolNumber || "",
      sealNumber: data?.sealNumber || "",
      itnNumber: data?.itnNumber || "",
      flightNo: data?.flightNo || "",
      shipmentkey: props?.data?.shipmentkey,
      trackingNumber: getShipmentNotesetails?.data?.shipmentNo || "",
    };
    dispatch(updateShipmentDetailsInProgress(req));
  };

  const errorAlertForTracking = (trackingNo) => {
    triggerNotification(notificationAPI, {
      type: NOTIFICATION_TYPES.ERROR,
      title: t('translation:default.notification.itSeems') + trackingNo + t('translation:default.notification.trackingNumber'),
    });
  };

  const pier2pier = (Option) => {
    if (Option == "1") {
      if (selectedNumber.length == 11) {
        URL =
          "https://www.pier2pier.com/links/tracking-random.php?i=" +
          selectedNumber +
          "&Client=nvisionglobal";
      } else {
        alert(
          t('translation:default.notification.itSeems') +
            selectedNumber +
           t('translation:visibility.containerNumbershouldbe')
        );
        return false;
      }
    } else if (Option == "2") {
      if (selectedNumber.length >= 5) {
        URL =
          "https://www.pier2pier.com/links/trackingvessel.php?VesselName=" +
          selectedNumber +
          "&Client=nvisionglobal";
      } else {
        alert(
         t('translation:visibility.youOnlyhave') +
            selectedNumber +
            t('translation:visibility.charactersRequired')
        );
        return false;
      }
    }
    window.open(URL, "_blank");
  };

  const handleTrackClick = (trackNo, mode) => {
    if (mode === SHIPMENT_TRACK_OPTIONS.TRACKING_NUMBER) {
      let url = false;
      if (
        getShipmentNotesetails?.data?.rateType === SHIPMENT_TRACKING_TYPES.UPS
      ) {
        url = `https://www.ups.com/track?loc=en_US&requester=QUIC&tracknum=${trackNo}/trackdetails`;
      } else if (
        getShipmentNotesetails?.data?.rateType === SHIPMENT_TRACKING_TYPES.FEDEX
      ) {
        url = `https://www.fedex.com/fedextrack/no-results-found?trknbr=${trackNo}`;
      } else if (
        getShipmentNotesetails?.data?.rateType === SHIPMENT_TRACKING_TYPES.DHL
      ) {
        url = `https://www.dhl.com/us-en/home/tracking/tracking-parcel.html?submit=1&tracking-id=${trackNo}`;
      }

      if (url) {
        window.open(url);
        return true;
      }
      alert(`${t('translation:visibility.invalidProvider')}!`)
    } else {
      setSelectedNumber(trackNo);
      pier2pier(mode);
    }
  };

  // const handlerOnTracking = (rateType, trackingNo, btnType) => {
  //   if (rateType == "UPS" && btnType === 1) {
  //     if (trackingNo.length == 18) {
  //       let URLTrack =
  //         "https://www.ups.com/track?loc=en_US&requester=QUIC&tracknum=" +
  //         trackingNo +
  //         "/trackdetails";
  //       window.open(URLTrack, "_blank");
  //     } else {
  //       errorAlertForTracking(trackingNo);
  //       return false;
  //     }
  //   } else if (rateType == "FEDEX" && btnType === 3) {
  //     if (trackingNo.length == 12) {
  //       let URLTrack =
  //         "https://www.fedex.com/fedextrack/no-results-found?trknbr=" +
  //         trackingNo;
  //       window.open(URLTrack, "_blank");
  //     } else {
  //       errorAlertForTracking(trackingNo);
  //       return false;
  //     }
  //   } else if (rateType == "DHL" && btnType === 2) {
  //     if (trackingNo.length >= 10) {
  //       let URLTrack =
  //         "https://www.dhl.com/us-en/home/tracking.html?locale=true&tracking-id=" +
  //         trackingNo;
  //       window.open(URLTrack, "_blank");
  //     } else {
  //       errorAlertForTracking(trackingNo);
  //       return false;
  //     }
  //   }
  // };

  return (
    <Row gutter={24} className="h-100">
      <Col sm={24}>
        <div className="h-90 d-flex flex-direction-column mt-15 pl-15">
          {getShipmentNotesetails.progressing == true && (
            <div className="d-flex justify-content-center align-items-center h-100">
              <Spin
                spinning={getShipmentNotesetails.progressing}
                tip={t('translation:default.loading')}
              ></Spin>
            </div>
          )}
          <Form onFinish={updateShipmentDetails} form={props.form}>
            <ScrollPagination>
              {/* {getShipmentNotesetails.data.map((item, index) => ( */}
              <Fragment>
                <Row gutter={5} className="pt-15 pb-15">
                  <Col sm={5}>
                    <FiledCard title={t('translation:visibility.detailCard.detailsTab.portOfOrigin')}>
                      <PrimaryLabel normal={true}>
                        {getShipmentNotesetails.data.portOfOrigin}
                      </PrimaryLabel>
                    </FiledCard>
                  </Col>
                  <Col sm={5}>
                    <FiledCard title={t('translation:visibility.detailCard.detailsTab.portOfDischarge')}>
                      <PrimaryLabel normal={true}>
                        {getShipmentNotesetails.data.portOfDischarge}
                      </PrimaryLabel>
                    </FiledCard>
                  </Col>
                  <Col sm={5}>
                    <FormInput
                      editMode={props.editDetailsBtnInfo?.isBtnClicked}
                      label={t('translation:visibility.detailCard.detailsTab.houseBill')}
                      placeholder={t('translation:visibility.detailCard.detailsTab.houseBill')}
                      name="bolNumber"
                      defaultValue={getShipmentNotesetails.data.bolNumber}
                      maxLength={25}
                    />
                  </Col>
                  <Col sm={6}>
                    <FiledCard title={t('translation:default.fields.containerNumber.label')}>
                      <PrimaryLabel normal={true}>
                        {getShipmentNotesetails.data.containerNumber}
                      </PrimaryLabel>
                    </FiledCard>
                  </Col>
                  <Col sm={3} className="pr-15">
                    <Button
                      disabled={
                        getShipmentNotesetails.data.containerNumber !== ""
                          ? false
                          : true
                      }
                      onClick={() =>
                        handleTrackClick(
                          getShipmentNotesetails.data.containerNumber,
                          1
                        )
                      }
                      className={`${
                        getShipmentNotesetails.data.containerNumber !== ""
                          ? "auction-btn"
                          : ""
                      } w-100  border-radion-5`}
                      type="primary"
                    >
                      {t('translation:default.buttons.track')}
                    </Button>
                  </Col>
                </Row>
                <Row gutter={5} className="pt-15 pb-15">
                  <Col sm={5}>
                    <FiledCard title={t('translation:visibility.detailCard.detailsTab.actualTransitDays')}>
                      <PrimaryLabel normal={true}>
                        {getShipmentNotesetails.data.actualTransitDays}
                      </PrimaryLabel>
                    </FiledCard>
                  </Col>
                  <Col sm={5}>
                    <FiledCard title={t('translation:visibility.detailCard.detailsTab.interlineRoutingSequence')}>
                      <PrimaryLabel normal={true}>
                        {getShipmentNotesetails.data.interlineRoutingSeq}
                      </PrimaryLabel>
                    </FiledCard>
                  </Col>
                  <Col sm={5}>
                    <FiledCard title={t('translation:visibility.detailCard.detailsTab.interlineSCAC')}>
                      <PrimaryLabel normal={true}>
                        {getShipmentNotesetails.data.interlineScac}
                      </PrimaryLabel>
                    </FiledCard>
                  </Col>
                  <Col sm={6}>
                    <FiledCard title={t('translation:visibility.detailCard.detailsTab.vessel')}>
                      <PrimaryLabel normal={true}>
                        {getShipmentNotesetails.data.vessel}
                      </PrimaryLabel>
                    </FiledCard>
                  </Col>
                  <Col sm={3} className="pr-15">
                    <Button
                      disabled={
                        getShipmentNotesetails.data.vessel !== "" ? false : true
                      }
                      onClick={() =>
                        handleTrackClick(getShipmentNotesetails.data.vessel, 3)
                      }
                      className={`${
                        getShipmentNotesetails.data.vessel !== ""
                          ? "auction-btn"
                          : ""
                      } w-100  border-radion-5`}
                      type="primary"
                    >
                      {t('translation:default.buttons.track')}
                    </Button>
                  </Col>
                </Row>
                <Row gutter={5} className="pt-15 pb-15">
                  <Col sm={5}>
                    <FormInput
                      editMode={props.editDetailsBtnInfo?.isBtnClicked}
                      label={t('translation:visibility.detailCard.detailsTab.flightVoyageNumber')}
                      placeholder={t('translation:visibility.detailCard.detailsTab.flightVoyageNumber')}
                      name="flightNo"
                      defaultValue={getShipmentNotesetails.data.flightNo}
                      maxLength={"50"}
                    />
                  </Col>
                  <Col sm={5}>
                    <FormInput
                      editMode={props.editDetailsBtnInfo?.isBtnClicked}
                      label={t('translation:visibility.detailCard.detailsTab.sealNumber')}
                      placeholder={t('translation:visibility.detailCard.detailsTab.sealNumber')}
                      name="sealNumber"
                      defaultValue={getShipmentNotesetails?.data?.sealNumber}
                      maxLength={50}
                    />
                  </Col>
                  <Col sm={5}>
                    <FormInput
                      editMode={props.editDetailsBtnInfo?.isBtnClicked}
                      label={t('translation:visibility.filters.shipmentSearch.itnNumber.label')}
                      placeholder={t('translation:visibility.filters.shipmentSearch.itnNumber.label')}
                      name="itnNumber"
                      defaultValue={getShipmentNotesetails?.data?.itnNumber}
                      maxLength={50}
                    />
                  </Col>
                  <Col sm={6}>
                    <FiledCard title={t('translation:default.fields.trackingNumber.label')}>
                      <PrimaryLabel normal={true}>
                        {getShipmentNotesetails.data.parceltrackingNumber}
                      </PrimaryLabel>
                    </FiledCard>
                  </Col>
                  <Col sm={3} className="pr-15">
                    <Button
                      disabled={
                        getShipmentNotesetails.data.parceltrackingNumber !== ""
                          ? false
                          : true
                      }
                      onClick={() =>
                        handleTrackClick(
                          getShipmentNotesetails.data.parceltrackingNumber,
                          2
                        )
                      }
                      className={`${
                        getShipmentNotesetails.data.parceltrackingNumber !== ""
                          ? "auction-btn"
                          : ""
                      } w-100  border-radion-5`}
                      type="primary"
                    >
                      {t('translation:default.buttons.track')}
                    </Button>
                  </Col>
                </Row>
              </Fragment>
              {/* ))} */}
            </ScrollPagination>
          </Form>
        </div>
      </Col>
    </Row>
  );
}

export default VisibilityDetailsTab;
