import { Row, Col, Form } from "antd";
import FormLabel from "../../../../components/Labels/FormLabel";
import FormDatePicker from "../../../../components/Inputs/FormDatePicker";
import FormInput from "../../../../components/Inputs/FormInput";
import FormSelectBox from "../../../../components/Inputs/FormSelectBox";
import CustomerNameSelector from "../../../../components/Selectors/CustomerNameSelector";
import ShipmentBranchSelector from "../../../../components/Selectors/ShipmentBranchSelector";
import { useDispatch, useSelector } from "react-redux";
import { useEffect, useMemo, useState } from "react";
import SupplierTypeSelector from "../../../../components/Selectors/SupplierTypeSelector";
import moment from "moment/moment";
import {
  getSearchVisibilityList,
  getSearchVisibilitySetDataList,
  getTransportationProvider,
} from "../../../../../redux/visibility/VisibilityActions";
import HiddenFormInput from "../../../../components/Inputs/HiddenFormInput";
import { useLocation } from "react-router-dom";
import { routeFileSelector } from "../../../../../utilz/i18n";
import { useTranslation } from "react-i18next";
import VisibilityBranchSelector from "../../../../components/Selectors/VisibilityBranchSelector";

const VisibilityShipmentSearch = ({ form }) => {
  const dispatch = useDispatch();
  const { shipmentDetails } = useSelector((state) => state.shipment);
  const { selectedCompany } = useSelector((state) => state.auth);
  const { transportationProviderList, visibilitySearchList } = useSelector(
    (state) => state.visibility
  );
  const [trackingNumber, setTrackingNumber] = useState("");
  const [aesNumber, setAESNumber] = useState("");
  const [transportationProviserOptions, setTransportationProviserOptions] =
    useState([]);

  const {
    user: { data: userDetails },
  } = useSelector((state) => state?.auth);
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);

  const pickupFromDate = Form.useWatch("pickupFromDate", form);
  const pickupToDate = Form.useWatch("pickupToDate", form);
  const createdonFromDate = Form.useWatch("createdonFromDate", form);
  const createdonToDate = Form.useWatch("createdonToDate", form);
  form.setFieldValue("openClose", "O");

  let shipmentFormData = form.getFieldsValue();
  if (
    shipmentFormData.shipmentType == "" ||
    typeof shipmentFormData.shipmentType == "undefined"
  ) {
    form.setFieldValue("shipmentType", "O");
  }

  useEffect(() => {
    setTrackingNumber(shipmentDetails?.data?.additionalDetails?.trailerNumber);
  }, [shipmentDetails?.data?.additionalDetails?.trailerNumber]);

  useEffect(() => {
    dispatch(
      getTransportationProvider({
        CompanyId: selectedCompany?.companyId,
        userType: userDetails?.userType,
      })
    );
    if (userDetails?.userType === "CUST") {
      form.setFieldValue("customerKey", selectedCompany?.companyId);
    }
  }, [selectedCompany?.companyId, userDetails?.userType]);

  useEffect(() => {
    if (
      transportationProviderList.data &&
      transportationProviderList.data.length > 0
    ) {
      let list = [];
      transportationProviderList.data.map((item) => {
        list.push({
          label: item.carrierName,
          value: item.carrierKey,
        });
      });
      setTransportationProviserOptions(list);
    }

    if (userDetails?.userType === "CARR") {
      form.setFieldValue(
        "carrierKey",
        transportationProviderList?.data?.[0]?.carrierKey || ""
      );
    }
  }, [transportationProviderList]);
  let customerKey = Form.useWatch("customerKey", form);

const callbackSetValue = (customerId) => {
    form.submit();
    
  }

  return (
    <Row className="pt-10">
      <Col sm={24}>
        {userDetails?.userType !== "CUST" && (
          <CustomerNameSelector
            name={"customerKey"}
            mode={""} //multiple
            form={form}
            setDetafuleValue={true}
            disabled={false}
            callbackSetValue = {(customerId)=>callbackSetValue(customerId)}
          />
        )}

        {userDetails?.userType == "CUST" && (
          <div>
            <HiddenFormInput name={"customerKey"} />
          </div>
        )}
      </Col>
      <Col sm={24}>
        {/* <ShipmentBranchSelector
          name="branchKey"
          label={"Branch"}
          placeholder={"Select Branch"}
          customerId={customerKey}
          margin={"mb-15"}
          mode="multiple"
        /> */}
        <VisibilityBranchSelector
          form={form}
          customerKey={customerKey}
          name={"branchKey"}
          label={t('translation:default.fields.branch.label')}
          mode="multiple"
          editMode={true}
        />
      </Col>

      <Col sm={24}>
        {userDetails?.userType !== "CARR" && (
          <FormSelectBox
            loading={false}
            options={transportationProviserOptions}
            placeholder={t('translation:default.fields.transportationProvider.selectPlaceholder')}
            label={t('translation:default.fields.transportationProvider.label')}
            rules={[]}
            name={"carrierKey"}
            editMode={true}
            margin={"mb-15"}
            mode="multiple"
          />
        )}
        {userDetails?.userType === "CARR" && (
          <div className="mb-15">
            {/* <FormLabel>{t('translation:default.fields.transportationProvider.label')}</FormLabel> 
                        <div className="mt-10 text-dark">{transportationProviderList?.data?.[0]?.carrierName}</div> */}
            <HiddenFormInput name={"carrierKey"} />
          </div>
        )}
      </Col>

      <Col sm={24}>
        <FormSelectBox
          loading={false}
          options={[
            {
              label: t('translation:default.options.all'),
              value: "A",
            },
            {
              label: t('translation:visibility.filters.shipmentSearch.shipmentType.optionOpen'),
              value: "O",
            },
            {
              label: t('translation:visibility.filters.shipmentSearch.shipmentType.optionClosed'),
              value: "C",
            },
          ]}
          placeholder={t('translation:default.fields.shipmentType.message')}
          label={t('translation:visibility.filters.shipmentSearch.shipmentType.label')}
          rules={[]}
          name={"shipmentType"}
          editMode={true}
          margin={"mb-15"}
          onChange={(e) => {
            let formdata = form.getFieldsValue();
            if (formdata.shipmentType == "O" || formdata.shipmentType == "A") {
              form.setFieldValue("createdonToDate", moment());
              form.setFieldValue(
                "createdonFromDate",
                moment().subtract(45, "days")
              );
            } else if (formdata.shipmentType == "C") {
              form.setFieldValue("createdonToDate", "");
              form.setFieldValue("createdonFromDate", "");
            }
          }}
        />
      </Col>

      <Col sm={24}>
        <SupplierTypeSelector
          name={"supplier"}
          label={t('translation:default.fields.supplier.label')}
          placeholder={t('translation:default.fields.supplier.placeholder')}
          margin={"mb-15"}
          customerKey={customerKey}
        />
      </Col>

      <Col sm={24}>
        <FormInput
          label={t('translation:default.fields.trackingNumber.label')}
          editMode={true}
          margin={"mb-15"}
          // defaultValue={trackingNumber}
          // onChange={(e) => setTrackingNumber(e.target.value)}
          name={"trackingNumber"}
          maxLength={100}
        />
      </Col>

      <Col sm={24} className={"mb-16"}>
        <FormLabel>{t('translation:visibility.filters.shipmentSearch.pickupDate.title')}</FormLabel>
        <Row className={"mt-3 align-items-center"} gutter={5}>
          <Col sm={6}>
            <FormLabel>{t('translation:default.fields.from.label')}</FormLabel>
          </Col>
          <Col sm={18}>
            <FormDatePicker
              placeholder={t('translation:default.fields.createdOn.placeholder')}
              margin={"mb-0"}
              name={"pickupFromDate"}
              disableFuture={true}
              disabledTargetDate={pickupToDate}
            />
          </Col>
        </Row>
        <Row className={"mt-6 align-items-center"} gutter={5}>
          <Col sm={6}>
            <FormLabel>{t('translation:default.fields.to.label')}</FormLabel>
          </Col>
          <Col sm={18}>
            <FormDatePicker
              name="pickupToDate"
              placeholder={t('translation:visibility.filters.shipmentSearch.pickupDate.placeholder')}
              margin={"mb-0"}
              disableFuture={false}
              disabledTargetDate={pickupFromDate}
            />
          </Col>
        </Row>
      </Col>
      <Col sm={24} className={"mb-16"}>
        <FormLabel>{t('translation:default.fields.createdOn.title')}</FormLabel>
        <Row className={"mt-3 align-items-center"} gutter={5}>
          <Col sm={6}>
            <FormLabel>{`${t('translation:default.fields.from.label')} :`}</FormLabel>
          </Col>
          <Col sm={18}>
            <FormDatePicker
              placeholder={t('translation:default.fields.createdOn.placeholder')}
              margin={"mb-0"}
              name={"createdonFromDate"}
              disableFuture={true}
              disabledTargetDate={createdonToDate}
            />
          </Col>
        </Row>
        <Row className={"mt-6 align-items-center"} gutter={5}>
          <Col sm={6}>
            <FormLabel>{`${t('translation:default.fields.to.label')} :`}</FormLabel>
          </Col>
          <Col sm={18}>
            <FormDatePicker
              name="createdonToDate"
              placeholder={t('translation:default.fields.createdOn.placeholder')}
              margin={"mb-0"}
              disableFuture={false}
              disabledTargetDate={createdonFromDate}
            />
          </Col>
        </Row>
      </Col>

      <Col sm={24}>
        <FormInput
          placeholder={t('translation:visibility.filters.shipmentSearch.itnNumber.label')}
          label={t('translation:visibility.filters.shipmentSearch.itnNumber.label')}
          editMode={true}
          name={"itnNumber"}
          maxLength={50}
          //defaultValue={aesNumber}
          //onChange={(e) => setAESNumber(e.target.value)}
        />
      </Col>
    </Row>
  );
};

export default VisibilityShipmentSearch;
