import { Checkbox, Col, Divider, Popconfirm, Row, Spin } from "antd";
import VerticalLine from "../../../components/Bars/VerticalLine";
import FooterContentCard from "../../common/FooterContentCard";
import { ReactComponent as FilesIcon } from "../../../../assets/icons/files-records.svg";
import { ReactComponent as OpenboxIcon } from "../../../../assets/icons/open-box.svg";
import { ReactComponent as WarningIcon } from "../../../../assets/icons/alert-exclamtion.svg";
import { ReactComponent as ExceptionIcon } from "../../../../assets/icons/alert-exclamtion-exception.svg";

import { useEffect, useState, useMemo } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  getFooterContentReq,
  getSearchVisibilityList,
  getSearchVisibilitySetDataList,
  getVisibilityExceptionReq,
} from "../../../../redux/visibility/VisibilityActions";
import { useLocation } from "react-router-dom";
import { routeFileSelector } from "../../../../utilz/i18n";
import { useTranslation } from "react-i18next";

const VisibilityFooter = (props) => {
  const [isCheckedAll, setIsCheckedAll] = useState(false);
  const [showExceptionPopup, setShowExceptionPopup] = useState(false);
  const [exceptionList, setExceptionList] = useState([]);

  const {
    user: { data: userDetails },
  } = useSelector((state) => state?.auth);
  const { selectedCompany } = useSelector((state) => state.auth);
  const { exceptionData, visibilitySearchList } = useSelector(
    (state) => state.visibility
  );

  const dispatch = useDispatch();
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);
  useEffect(() => {
    // const getLoadException = () => {
    //   let req = {
    //     UserType: userDetails?.userType,
    //     CarrierKey:
    //       userDetails?.userType === "CARR"
    //         ? `${selectedCompany?.companyId}`
    //         : '',
    //     CustomerKey:
    //       userDetails?.userType === "CUST"
    //         ? `${selectedCompany?.companyId}`
    //         : "",
    //     VendorKey: "",
    //     StatusCode: "",
    //   };
    //   dispatch(getVisibilityExceptionReq(req));
    // };

    if (selectedCompany) {
      setIsCheckedAll(false);
      setExceptionList([]);
      setShowExceptionPopup(false);
    }
  }, [selectedCompany]);

  useEffect(() => {
    if (visibilitySearchList?.exceptionData?.length > 0) {
      let tempData = [];
      visibilitySearchList?.exceptionData.map((item, index) => {
        tempData.push({ ...item, isChecked: false });
      });
      setExceptionList(tempData);
      setIsCheckedAll(false);
    } else if (
      visibilitySearchList?.exceptionData?.length == 0 &&
      exceptionList.length > 0
    ) {
      setExceptionList([]);
      setIsCheckedAll(false);
    }
  }, [visibilitySearchList]);

  const isCheckedAllhandler = (e) => {
    let temp = [...exceptionList];
    temp.forEach((item) => {
      item.isChecked = !isCheckedAll;
    });
    setIsCheckedAll(!isCheckedAll);
    setExceptionList(temp);
  };

  const toFindAllChecked = () => {
    let findVal = exceptionList.filter((item) => item.isChecked === false);
    setIsCheckedAll(findVal.length > 0 ? false : true);
  };

  const updateExceptionHandler = (index) => {
    let temp = [...exceptionList];
    temp[index]["isChecked"] = !temp[index]["isChecked"];
    setExceptionList(temp);
    toFindAllChecked();
  };

  const clearExceptionHandler = () => {
    let temp = [...exceptionList];
    temp.forEach((item) => {
      item.isChecked = false;
    });
    setIsCheckedAll(false);
    setExceptionList(temp);
    setShowExceptionPopup(false);
  };

  const searchExceptionHandler = () => {
    let findSelectedItem = exceptionList
      .filter((item) => item.isChecked === true)
      .map((i) => i.statusCode);
    dispatch(getSearchVisibilitySetDataList([]));
    if (findSelectedItem.length > 0) {
      dispatch(
        getSearchVisibilityList({
          statusCode: findSelectedItem.join(","),
          searchFlag: "Y",
        })
      );
      // dispatch(getFooterContentReq({
      //   ...visibilitySearchList.req,
      //   statusCode: findSelectedItem.join(","),
      //   searchFlag: "Y",
      //   userType: userDetails?.userType,
      //   carrierKey:
      //     userDetails?.userType === "CARR"
      //       ? `${selectedCompany?.companyId}`
      //       : '',
      //   customerKey:
      //     userDetails?.userType === "CUST"
      //       ? `${selectedCompany?.companyId}`
      //       : "",
      // }));
    } else {
      // dispatch(
      //   getSearchVisibilityList({
      //     statusCode: "",
      //     searchFlag: "",
      //   })
      // );
      // dispatch(getFooterContentReq({
      //   ...visibilitySearchList.req,
      //   statusCode: findSelectedItem.join(","),
      //   searchFlag: "Y",
      //   userType: userDetails?.userType,
      //   carrierKey:
      //     userDetails?.userType === "CARR"
      //       ? `${selectedCompany?.companyId}`
      //       : '',
      //   customerKey:
      //     userDetails?.userType === "CUST"
      //       ? `${selectedCompany?.companyId}`
      //       : "",
      // }));
    }
    setShowExceptionPopup(false);
  };

  return (
    <Row
      justify={"space-around"}
      className=" footer-container background-header px-20  py-12"
    >
      <Col>
        <FooterContentCard
          count={
            props.visibilitySearchList?.footerData?.[0]?.totalShipment || 0
          }
          label={t("translation:visibility.footer.totalShipments")}
          Icon={FilesIcon}
        />
      </Col>
      <Col>
        <VerticalLine />
      </Col>
      <Col>
        <FooterContentCard
          count={props.visibilitySearchList?.footerData?.[0]?.openShipment || 0}
          label={t("translation:visibility.footer.openShipments")}
          Icon={OpenboxIcon}
        />
      </Col>
      <Col>
        <VerticalLine />
      </Col>
      <Col>
        <FooterContentCard
          count={
            props.visibilitySearchList?.footerData?.[0]?.closeShipment || 0
          }
          label={t("translation:visibility.footer.closedShipments")}
          Icon={WarningIcon}
        />
      </Col>
      <Col>
        <VerticalLine />
      </Col>
      <Col>
        <FooterContentCard
          count={
            props.visibilitySearchList?.footerData?.[0]?.exceptionShipment || 0
          }
          label={
            props.visibilitySearchList?.footerData?.[0]?.exceptionShipment > 0
              ? t("translation:visibility.footer.exceptions")
              : "No Exceptions"
          }
          Icon={ExceptionIcon}
          color="red"
          labelClass={
            props.visibilitySearchList?.footerData?.[0]?.exceptionShipment > 0
              ? "text-danger"
              : ""
          }
          contentClass={
            props.visibilitySearchList?.footerData?.[0]?.exceptionShipment > 0
              ? "text-danger"
              : ""
          }
          onClick={() => setShowExceptionPopup(!showExceptionPopup)}
        />
      </Col>
      <Popconfirm
        visible={showExceptionPopup}
        overlayStyle={{}}
        overlayClassName={"visibility-exception-popup-wrapper"}
        overlayInnerStyle={{ background: "#38414A" }}
        placement="topLeft"
        icon={""}
        okText={t("translation:default.buttons.clear")}
        cancelText={t("translation:default.buttons.select")}
        cancelButtonProps={{
          className: "visibility-exception-btn",
          size: "large",
        }}
        okButtonProps={{
          className: "visibility-exception-btn",
          size: "large",
        }}
        onCancel={() => searchExceptionHandler()}
        onConfirm={() => clearExceptionHandler()}
        title={() => {
          return (
            <div className="visibility-footer-excetion-container">
              <Checkbox
                checked={isCheckedAll}
                value={isCheckedAll}
                onChange={(e) => isCheckedAllhandler(e)}
              >
                {t("translation:visibility.footer.exceptions")}{" "}
                {`(${exceptionList.reduce(
                  (totalCount, item) =>
                    (totalCount = totalCount + item.exceptionCount),
                  0
                )})`}
              </Checkbox>
              <Divider className="exception-divider" />
              <div className="exception-container">
                <Spin
                  className="d-flex justify-content-center align-items-center h-100"
                  size={"large"}
                  tip={t("translation:default.loading")}
                  spinning={exceptionData.progressing}
                />
                {exceptionList.map((obj, index) => {
                  return (
                    <div>
                      <Checkbox
                        checked={obj.isChecked}
                        onChange={() => updateExceptionHandler(index)}
                      >
                        {obj.statusDescription} ({obj.exceptionCount})
                      </Checkbox>
                      <Divider
                        style={{ background: "#FFFFFF", marginTop: 15 }}
                      />
                    </div>
                  );
                })}
              </div>
            </div>
          );
        }}
      />
    </Row>
  );
};

export default VisibilityFooter;
