import { Col, Form, Row } from "antd";
import { useState, useEffect, useMemo } from "react";
import Button from "../../../components/Buttons/Button";
import FiledCard from "../../../components/Cards/FiledCard";
import BatchLabel from "../../../components/Labels/BatchLabel";
import PrimaryLabel from "../../../components/Labels/PrimaryLabel";
import Paper from "../../../components/Paper/Paper";
import Tab from "../../../components/Tabs/Tab";
import GeneralTab from "./tabs/GeneralTab";
import TrackingStatusDetails from "./tabs/TrackingStatusDetails";
import VisibilityDetailsTab from "./tabs/VisibilityDetailsTab";
import ProductInfoTab from "./tabs/ProductInfoTab";
import FileDetailsTab from "./tabs/FileDetailsTab";
import { useSelector, useDispatch } from "react-redux";
import {
  addNewFileBtn,
  addNewTrackingStatusBtn,
  editGeneralBtn,
  editDetailsBtn,
  getShipmentNotesAndDetails,
  getShipperConsigneeDetails,
} from "../../../../redux/visibility/VisibilityActions";
import VisibilityShipmentNoteTab from "./tabs/VisibilityShipmentNoteTab";
import SaveCancelButton from "../../../components/Common/SaveCancelButton";
import { useLocation } from "react-router-dom";
import { routeFileSelector } from "../../../../utilz/i18n";
import { useTranslation } from "react-i18next";

const VisibilityDetailCardContainer = (props) => {
  const [findShipmentType, setFindShipmentType] = useState("");
  const { shipmentListTypes } = useSelector((state) => state.shipment);
  const [activeTabName, setActiveTabName] = useState("general");
  const {
    addnewfileactions,
    addNewStatusAction,
    editGeneralBtnInfo,
    editDetailsBtnInfo,
  } = useSelector((state) => state.visibility);
  const {
    user: { data: userDetails },
  } = useSelector((state) => state?.auth);
  const [form] = Form.useForm();

  const dispatch = useDispatch();
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);

  useEffect(() => {
    if (
      shipmentListTypes.data &&
      shipmentListTypes.data.length > 0 &&
      typeof props.data.shipmentType != "undefined" &&
      props.data.shipmentType != ""
    ) {
      let findRecord = shipmentListTypes.data.filter(
        (item) =>
          item.codeId?.toLowerCase() === props.data.shipmentType?.toLowerCase()
      );
      if (findRecord && findRecord.length > 0) {
        setFindShipmentType(findRecord[0].codeDesc);
      }
    }
  }, [shipmentListTypes, props.data.shipmentType]);

  useEffect(() => {
    if (props.data.shipmentNo) {
      dispatch(
        getShipmentNotesAndDetails({ shipmentNumber: props.data.shipmentNo })
      );
      dispatch(
        getShipperConsigneeDetails({ shipmentNumber: props.data.shipmentNo })
      );
    }
  }, [dispatch, props.data.shipmentNo]);

  return (
    <Paper className="pt-15 pb-15 border-muted h-100">
      <Row className="pl-15">
        <Col sm={4}>
          <FiledCard title={t('translation:default.fields.shipmentNumber.placeholder')}>
            <PrimaryLabel className="text-highlight font-weight-600">
              {props.data.shipmentNo}
            </PrimaryLabel>
          </FiledCard>
        </Col>
        <Col sm={4}>
          <FiledCard title={t('translation:default.fields.shipmentType.label')}>
            <BatchLabel className={"mt-4 bg-success"}>
              {findShipmentType}
            </BatchLabel>
          </FiledCard>
        </Col>
        <Col sm={16}>
          <FiledCard title={`${t('translation:visibility.trackingStatusInfo.trackingStatus.label')}:`}>
            <PrimaryLabel className="text-highlight font-weight-600">
              {props.data.shipmentStatusTypeName}
            </PrimaryLabel>
          </FiledCard>
        </Col>
      </Row>
      <hr />
      <Row className="h-100">
        <Col
          sm={24}
          className={
            "h-100 pb-15 overflow-y-inherit d-flex flex-direction-column"
          }
        >
          <div
            className={`h-100 overflow-y-inherit d-flex flex-direction-column`}
          >
            <div className="d-flex flex-direction-column overflow-hidden h-100 br-4">
              <div className="custom-scroll-bar overflow-hidden mb-15 flex-1">
                <Tab
                  className={`visibility-tabs ${
                    (activeTabName == "general" &&
                      editGeneralBtnInfo?.isBtnClicked === false) ||
                    activeTabName == "tracking" ||
                    activeTabName == "details" ||
                    activeTabName == "fileDetails"
                      ? "reduce-tab-width"
                      : "full-width-tab"
                  } 
                  ${
                    activeTabName == "general" && 
                    editGeneralBtnInfo?.isBtnClicked === true
                      ? "reduce-tab-width-75"
                      : ""
                  }`}
                  onChange={(activeKey) => {
                    setActiveTabName(activeKey);
                  }}
                  activeKey={activeTabName}
                  defaultActiveKey="general"
                  contents={[
                    {
                      title: t('translation:visibility.detailCard.tabs.general'),
                      content: (
                        <GeneralTab
                          {...props}
                          form={form}
                          editGeneralBtnInfo={editGeneralBtnInfo}
                          userDetails={userDetails}
                          viewMoreStatusClick={() =>
                            setActiveTabName("tracking")
                          }
                        />
                      ),
                      contentKey: "general",
                    },
                    {
                      title: t('translation:visibility.detailCard.tabs.tracking'),
                      content: <TrackingStatusDetails {...props} />,
                      contentKey: "tracking",
                    },
                    {
                      title: t('translation:default.fields.shipmentNotes'),
                      content: <VisibilityShipmentNoteTab {...props} />,
                      contentKey: "notes",
                    },
                    {
                      title: t('translation:default.fields.shipmentDetails'),
                      content: 
                        <VisibilityDetailsTab 
                          {...props} 
                          form={form}
                          editDetailsBtnInfo={editDetailsBtnInfo}
                        />,
                      contentKey: "details",
                    },
                    {
                      title: t('translation:visibility.detailCard.tabs.productInfo'),
                      content: <ProductInfoTab {...props} />,
                      contentKey: "productInfo",
                    },
                    {
                      title: t('translation:default.documentManager.title'),
                      content: <FileDetailsTab {...props} />,
                      contentKey: "fileDetails",
                    },
                  ]}
                ></Tab>
              </div>
            </div>
          </div>
          {activeTabName == "general" && (
            <div className="visibility-file-add-new-file-btn">
              {editGeneralBtnInfo?.isBtnClicked === false && (
                <Button
                  onClick={() => dispatch(editGeneralBtn())}
                  className={`auction-btn w-100  border-radion-5`}
                  buttonType="button"
                  type="primary"
                >
                  {t('translation:default.buttons.edit')}
                </Button>
              )}
              {editGeneralBtnInfo?.isBtnClicked === true && (
                <SaveCancelButton
                  hideDeleteButton={true}
                  isNew={false}
                  editMode={true}
                  setEditMode={() => {}}
                  onCancel={() => dispatch(editGeneralBtn())}
                  handleRemove={() => {}}
                  onSave={() => {
                    form.submit();
                  }}
                />
              )}
            </div>
          )}

          {activeTabName == "details" && (
            <div className="visibility-file-add-new-file-btn">
              {editDetailsBtnInfo?.isBtnClicked === false && (
                <Button
                  onClick={() => dispatch(editDetailsBtn())}
                  className={`auction-btn w-100  border-radion-5`}
                  buttonType="button"
                  type="primary"
                >
                  {t('translation:default.buttons.edit')}
                </Button>
              )}
              {editDetailsBtnInfo?.isBtnClicked === true && (
                <SaveCancelButton
                  hideDeleteButton={true}
                  isNew={false}
                  editMode={true}
                  setEditMode={() => {}}
                  onCancel={() => dispatch(editDetailsBtn())}
                  handleRemove={() => {}}
                  onSave={() => {
                    form.submit();
                  }}
                />
              )}
            </div>
          )}

          {userDetails?.userType === "CARR" && activeTabName == "tracking" && (
            <div className="visibility-file-add-new-file-btn">
              <Button
                onClick={() => dispatch(addNewTrackingStatusBtn(true))}
                className={`${
                  addNewStatusAction.btnCliked ? "" : "auction-btn"
                } w-100  border-radion-5`}
                disabled={addNewStatusAction.btnCliked}
                buttonType="button"
                type="primary"
              >
                {t('translation:default.buttons.newStatus')}
              </Button>
            </div>
          )}

          {activeTabName == "fileDetails" && (
            <div className="visibility-file-add-new-file-btn">
              <Button
                onClick={() => dispatch(addNewFileBtn(true))}
                className={`${
                  addnewfileactions.btnCliked ? "" : "auction-btn"
                } w-100  border-radion-5`}
                disabled={addnewfileactions.btnCliked}
                buttonType="button"
                type="primary"
              >
                {t('translation:default.documentManager.addNewFile')}
              </Button>
            </div>
          )}
        </Col>
      </Row>
    </Paper>
  );
};

export default VisibilityDetailCardContainer;
