import { Card, Col, Row, Spin } from "antd";
import React, { useEffect, useMemo } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getVisibilityProductInfo } from "../../../../../redux/visibility/VisibilityActions";
import FiledCard from "../../../../components/Cards/FiledCard";
import PrimaryLabel from "../../../../components/Labels/PrimaryLabel";
import { useLocation } from "react-router-dom";
import { routeFileSelector } from "../../../../../utilz/i18n";
import { useTranslation } from "react-i18next";

function ProductInfoTab(props) {
  const dispatch = useDispatch();
  const { productInfo } = useSelector((state) => state.visibility);
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);

  useEffect(() => {
    if (props.data?.shipmentNo) {
      dispatch(
        getVisibilityProductInfo({ shipmentNumber: props.data.shipmentNo })
      );
    }
  }, [props.data.shipmentNo]);

  return (
    <div className="d-flex flex-direction-column overflow-hidden h-100 br-4 visibility-product-info-tab">
      <div className="custom-scroll-bar overflow-x-hidden mb-15 flex-1 pl-15 pr-15">
        {productInfo.progressing == true && (
          <div className="d-flex justify-content-center align-items-center h-100">
            <Spin spinning={productInfo.progressing} tip={t('translation:default.loading')}></Spin>
          </div>
        )}
        {productInfo.data.map((item, index) => (
          <Card
            key={index}
            title={
              <FiledCard
                title={t('translation:visibility.detailCard.productInfoTab.productCode')}
                childClassName="pl-10"
                textClass="d-flex align-items-center"
              >
                <PrimaryLabel normal={true} className="font-size-16">
                  {item.productCode}
                </PrimaryLabel>
              </FiledCard>
            }
            className="mb-15"
          >
            <Row>
              <Col sm={8} className="pr-15">
                <FiledCard title={t('translation:visibility.detailCard.productInfoTab.productDescription')}>
                  <PrimaryLabel normal={true} className="font-weight-400">
                    {item.productDescription}
                  </PrimaryLabel>
                </FiledCard>
              </Col>
              <Col sm={16} className="pr-15">
                <Row className="mb-15">
                  <Col sm={8}>
                    <FiledCard title={t('translation:default.fields.quantity.label')}>
                      <PrimaryLabel
                        normal={true}
                        className="field-card-container field-card-title"
                      >
                        {item.quantity}
                      </PrimaryLabel>
                    </FiledCard>
                  </Col>
                  <Col sm={8}>
                    <FiledCard title={t('translation:default.fields.unitOfMeasure')}>
                      <PrimaryLabel
                        normal={true}
                        className="field-card-container field-card-title"
                      >
                        {item.unitMeasure}
                      </PrimaryLabel>
                    </FiledCard>
                  </Col>
                  <Col sm={8}>
                    <FiledCard title={t('translation:visibility.detailCard.productInfoTab.grossWeight')}>
                      <PrimaryLabel
                        normal={true}
                        className="field-card-container field-card-title"
                      >
                        {item.grossWeight}
                      </PrimaryLabel>
                    </FiledCard>
                  </Col>
                </Row>
                <Row className="mb-15">
                  <Col sm={16}>
                    <FiledCard title={t('translation:default.fields.packageBilledQuantityunitOfMeasure')}>
                      <PrimaryLabel
                        normal={true}
                        className="field-card-container field-card-title"
                      >
                        {item.uomBillQty}
                      </PrimaryLabel>
                    </FiledCard>
                  </Col>
                  <Col sm={8}>
                    <FiledCard title={t('translation:default.fields.purchaseOrderNumber')}>
                      <PrimaryLabel
                        normal={true}
                        className="field-card-container field-card-title"
                      >
                        {item.lineNumber}
                      </PrimaryLabel>
                    </FiledCard>
                  </Col>
                  <Col sm={8}>
                    <FiledCard title={t('translation:default.fields.purchaseOrderNumber')}>
                      <PrimaryLabel
                        normal={true}
                        className="field-card-container field-card-title"
                      >
                        {item.poNumber}
                      </PrimaryLabel>
                    </FiledCard>
                  </Col>
                </Row>
                <Row className="mb-15">
                <Col sm={8}>
                    <FiledCard title={t('translation:default.fields.packageLoadType.label')}>
                      <PrimaryLabel
                        normal={true}
                        className="field-card-container field-card-title"
                      >
                        {item.billQty}
                      </PrimaryLabel>
                    </FiledCard>
                  </Col>
                  <Col sm={8}>
                    <FiledCard title={t('translation:default.fields.palletLoadType.label')}>
                      <PrimaryLabel
                        normal={true}
                        className="field-card-container field-card-title"
                      >
                        {item.palletLoadType}
                      </PrimaryLabel>
                    </FiledCard>
                  </Col>
                  <Col sm={8}>
                    <FiledCard title={t('translation:default.fields.salesOrderNumber')}>
                      <PrimaryLabel
                        normal={true}
                        className="field-card-container field-card-title"
                      >
                        {item.uomWeight}
                      </PrimaryLabel>
                    </FiledCard>
                  </Col>
                </Row>
                <Row className="mb-15">
                    <Col sm={16}>
                      <FiledCard title={t('translation:default.fields.salesOrderNumber')}>
                        <PrimaryLabel
                          normal={true}
                          className="field-card-container field-card-title"
                        >
                          {item.salesOrder}
                        </PrimaryLabel>
                      </FiledCard>
                    </Col>
                  </Row>
              </Col>
            </Row>
          </Card>
        ))}
      </div>
    </div>
  );
}

export default ProductInfoTab;
