import { Col, Form, Row } from "antd";
import FilterContent from "./components/search/VisibilityFilterContent";
import Button from "../../components/Buttons/Button";
import { VISIBILITY_DATE_FIELDS } from "../../../contants/Fields";
import { DEFAULT_DATE_FORMAT } from "../../../contants/common";
import { useDispatch, useSelector } from "react-redux";
import {
  clearSelectedVisibility,
  getSearchVisibilityList,
  getSearchVisibilitySetDataList,
  getVisibilityExceptionClear,
} from "../../../redux/visibility/VisibilityActions";
import { useEffect, useMemo } from "react";
import { setShipmentData } from "../../../redux/shipment/ShipmentActions";
import moment from "moment";
import { useLocation } from "react-router-dom";
import { routeFileSelector } from "../../../utilz/i18n";
import { useTranslation } from "react-i18next";

const VisibilityFilter = (props) => {
  const [form] = Form.useForm();
  const dispatch = useDispatch();
  const { visibilitySearchList } = useSelector((state) => state.visibility);
  const {
    user: { data: userDetails },
  } = useSelector((state) => state?.auth);
  const { selectedCompany } = useSelector((state) => state.auth);
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);

  const handleFilterSubmit = (data) => {
    dispatch(getSearchVisibilitySetDataList([]));
    dispatch(getVisibilityExceptionClear());
    dispatch(setShipmentData([]));
    dispatch(clearSelectedVisibility({}));

    for (const key in data) {
      if (VISIBILITY_DATE_FIELDS.includes(key) && data[key]) {
        data[key] =
          typeof data[key].format(DEFAULT_DATE_FORMAT) === "undefined"
            ? ""
            : data[key].format(DEFAULT_DATE_FORMAT) + "";
      } else {
        data[key] = data[key] == null ? "" : data[key] + "";
      }
    }
    if (typeof data?.shipmentType == "") {
      data.shipmentType = "O";
    }
    let req = {
      vendorKey:
        userDetails?.userType === "VEND" && data?.supplier == ""
          ? selectedCompany?.companyId + ""
          : data.supplier || "",
      consigneeKey: "",
      pageIndex: 1,
      pageSize: 10,
      ...data,
      customerKey: data.customerKey + "",
      statusCode:''
    };
    dispatch(getSearchVisibilityList(req));

    // dispatch(getFooterContentReq({
    //   ...data,
    //   "shipmentType": data.shipmentType,
    //   "customerKey": userDetails?.userType === "CUST"? data.customerKey:'',
    //   "carrierKey": userDetails?.userType === "CARR" ? data.carrierKey:'',
    //   "vendorKey": "",
    //   "userType":userDetails?.userType,
    // }));
  };

  const handleClear = () => {
    form.resetFields();
    let formKey = {};
    if (userDetails?.userType === "CUST") {
      formKey.customerKey = `${selectedCompany?.companyId}`;
    } else if (userDetails?.userType === "CARR") {
      formKey.carrierKey = `${selectedCompany?.companyId}`;
    }
    form.setFieldsValue({
      openClose: "O",
      shipmentType: "O",
      pickupFromDate: moment(new Date()).subtract(3, "months"),
      pickupToDate: moment(new Date()),
      ...formKey,
    });
  };

  useEffect(() => {
    if (selectedCompany) {
      handleClear();
      if (userDetails?.userType === "CUST") {
        setTimeout(() => {
          form.submit();
        }, 500);
      }
    }
  }, [selectedCompany]);

  return (
    <div className="d-flex flex-direction-column overflow-hidden h-100 br-4">
      <div className="custom-scroll-bar overflow-x-hidden mb-15 flex-1">
        <Form
          onFinish={handleFilterSubmit}
          form={form}
          initialValues={{
            createdByType: 1,
            openClose: "O",
            shipmentType: "O",
            pickupFromDate: moment(new Date()).subtract(3, "months"),
            pickupToDate: moment(new Date()),
          }}
        >
          <FilterContent form={form} />
        </Form>
      </div>
      <Row className="px-15" gutter={15}>
        <Col sm={24} md={12}>
          <Button
            fullWidth={true}
            type={"default"}
            onClick={handleClear}
            buttonType="button"
          >
            {t('translation:default.buttons.clear')}
          </Button>
        </Col>
        <Col sm={24} md={12}>
          <Button
            fullWidth={true}
            type={"primary"}
            onClick={() => form.submit()}
            buttonType="submit"
          >
            {t('translation:default.buttons.search')}
          </Button>
        </Col>
      </Row>
    </div>
  );
};

export default VisibilityFilter;
