import { Form, Input } from "antd";
import React, { useEffect, useMemo, useState } from "react";
import { useSelector } from "react-redux";
import { getShipperConsigneeSearchResult } from "../../../../../redux/visibility/VisibilityEffects";
import FormSelectBox from "../../../../components/Inputs/FormSelectBox";
import CustomComponent from "./CustomCompent";
import { useLocation } from "react-router-dom";
import { routeFileSelector } from "../../../../../utilz/i18n";
import { useTranslation } from "react-i18next";
import i18n from 'i18next';

const ShipperConsigneeSearchSelector = ({
  placeholder = i18n.t('translation:default.fields.shipperName.placeholder'),
  label =  i18n.t('translation:default.fields.shipperName.label'),
  rules = [],
  name,
  form = {},
  onChange = () => {},
  customerKey,
  searchType,
  userType,
  margin,
}) => {
  const { selectedCompany } = useSelector((state) => state.auth);
  const [params, setParams] = useState({});
  const formFieldValue = Form.useWatch(name, form);
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);

  useEffect(() => {
    if (userType === "CARR") {
      if (searchType === "shipper") {
        setParams({
          carrierKey: selectedCompany?.companyId,
          shipperFlag: "Y",
        });
      } else {
        setParams({
          carrierKey: selectedCompany?.companyId,
          consigneeFlag: "Y",
        });
      }
    } else {
      if (searchType === "shipper") {
        setParams({
          customerKey: selectedCompany?.companyId + "",
          shipperFlag: "Y",
        });
      } else {
        setParams({
          customerKey: selectedCompany?.companyId + "",
          consigneeFlag: "Y",
        });
      }
    }
  }, [selectedCompany, userType, searchType]);

  const handleChange = (val) => {
    onChange(val ? val : {});
    form.setFieldsValue({ [name]: val?.name });
  };

  return (
    <>
      <FormSelectBox
        placeholder={placeholder}
        label={label}
        margin={margin}
        rules={rules}
        asyncFetchMode={true}
        asynchFuntion={getShipperConsigneeSearchResult}
        defaultParams={params}
        onChange={handleChange}
        value={formFieldValue} //form.getFieldValue(name)
        accessors={{ label: "name", value: "shipRecKey" }}
        CustomOptionsComponent={CustomComponent}
        onClear={() => form.setFieldsValue({ [name]: undefined })}
        userType={userType}
        setRecord="shipperconsig"
      />
      <Form.Item name={name} className={"d-none"}>
        <Input />
      </Form.Item>
    </>
  );
};

export default ShipperConsigneeSearchSelector;
